<template>
  <div class="ui left aligned raised container segment" :class="color">
    <div class="ui pointing menu">
      <div class="ui container">
        <div class="item">
          <select class="ui dropdown" v-model="node">
            <option v-for="(dev, idx) in devices" :value="dev.node" :key="idx">N{{dev.node}} - {{dev.name}}</option>
          </select>
        </div>
        <div class="item">
          <button :class="primaryBtnState" @click="refreshCurrentValues">
            <i class="ui upload icon"></i>Traer Confg. Actual
          </button>
        </div>
        <div class="right menu">
          <div class="item">
            <button :class="primaryBtnState" @click="saveButtonsConfig">
              <i class="ui download icon"></i>Enviar Confg. al Nodo
            </button>
          </div>
        </div>
      </div>
    </div>
    <form class="ui form">
      <table class="ui basic table">
        <tbody>
          <tr>
            <td rowspan="8"><img src="../../static/images/Thermostat.jpg" width="100%"></td>
            <td class="center aligned"><i class="large power off icon"></i></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables4" class="ui checkbox" type="checkbox">
                <label>Bot&oacute;n ON/OFF (Desocupado/Standby)</label>
              </div>
              <div v-if="nciEnables4 != 0" class="grouped fields">
                <div :class="inlineFieldState">
                  <input class="input" type="radio" name="setStandby" id="nciCfg4_0" value=0 v-model="nciConfig4">
                  <label for="nciCfg4_0">
                    <span class="hiligthed">Desocupado</span>: Este bot&oacute;n pone la unidad en estado DESOCUPADO
                  </label>
                </div>
                <div :class="inlineFieldState">
                  <input class="input" type="radio" name="setOff" id="nciCfg4_1" value=1 v-model="nciConfig4">
                  <label for="nciCfg4_1">
                    <span class="hiligthed">Standby</span>: Este bot&oacute;n pone unidad en estado STANDBY
                  </label>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td class="center aligned"><i class="large sort icon"></i></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables5" class="ui checkbox" type="checkbox">
                <label>Botones para Modificar Temperatura de Consigna</label>
              </div>
            </td>
          </tr>
          <tr>
            <td class="center aligned"><span class="tempfont"><sup>o</sup>C/<sup>o</sup>F</span></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables7" class="ui checkbox" type="checkbox">
                <label>Bot&oacute;n Grados Celsius/Fahrenheit</label>
              </div>
              <div v-if="nciEnables7 != 0" class="grouped fields">
                <div :class="inlineFieldState">
                  <input class="input" type="radio" name="setCelsius" id="nciCfg6_0" value=0 v-model="nciConfig6">
                  <label for="nciCfg6_0">
                    <span class="hiligthed">Celsius</span>: Iniciar mostrando temperaturas en Grados Celsius
                  </label>
                </div>
                <div :class="inlineFieldState">
                  <input class="input" type="radio" name="setFheit" id="nciCfg6_1" value=1 v-model="nciConfig6">
                  <label for="nciCfg6_1">
                    <span class="hiligthed">Fahrenheit</span>: Iniciar mostrando temperaturas en Grados Fahrenheit
                  </label>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td class="center aligned"><i class="large sun icon"></i>/<i class="large snowflake icon"></i></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables6" class="ui checkbox" type="checkbox">
                <label>Bot&oacute;n Frio, Calor, Ventilaci&oacute;n, Autom&aacute;tico</label>
              </div>
            </td>
          </tr>
          <tr>
            <td class="center aligned"><img src="../../static/images/fanicon.png" width="24px"></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables8" class="ui checkbox" type="checkbox">
                <label>Bot&oacute;n Velocidad de Ventilaci&oacute;n</label>
              </div>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables9" class="ui checkbox" type="checkbox">
                <label>Mostrar Temperatura Ambiente en Pantalla</label>
              </div>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables0" class="ui checkbox" type="checkbox">
                <label>Alarma de Temperatura por Exceso</label>
              </div>
            </td>
          </tr>
          <tr>
            <td></td>
            <td>
              <div :class="inlineFieldState">
                <input v-model="nciEnables1" class="ui checkbox" type="checkbox">
                <label>Alarma de Temperatura por Defecto</label>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </form>
  </div>
</template>

<script>
export default {

  data () {
    return {
      node: 0,
      devices: [],
      nciEnables0: 1,
      nciEnables1: 1,
      nciEnables4: 1,
      nciEnables5: 1,
      nciEnables6: 1,
      nciEnables7: 1,
      nciEnables8: 1,
      nciEnables9: 1,
      nciConfig4: 0,
      nciConfig6: 0
    }
  },

  created () {
    this.refreshCurrentValues()
    this.loadDevices()
  },

  methods: {
    loadDevices () {
      this.color = 'red'
      var msg = { type: 'db.getDevices', queryid: 'CLI-GETDEVICES' }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    devicesArrived () {
      if (!this.$store.state.db.devices.length) return
      this.color = 'green'
      var devices = []
      for (var i = 0; i < this.$store.state.db.devices.length; i++) {
        if (this.$store.state.db.devices[i].node) {
          devices.push(this.$store.state.db.devices[i])
        }
      }
      this.devices = devices
      this.$store.commit('SET_DEVICES_FROM_DB', null)
    },

    getCfgPackNvs () {
      var msg = {
        type: 'getNvPackValues',
        node: this.node,
        nvPack: 'nciConfig[4],nciConfig[6],nciEnables[4],nciEnables[5],nciEnables[6],nciEnables[7],nciEnables[8],nciEnables[9],nciEnables[0],nciEnables[1]'
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    refreshCurrentValues () {
      this.nciConfig4 = this.$store.state.btnEnables.nciConfig4
      this.nciConfig6 = this.$store.state.btnEnables.nciConfig6
      this.nciEnables0 = this.$store.state.btnEnables.nciEnables0
      this.nciEnables1 = this.$store.state.btnEnables.nciEnables1
      this.nciEnables4 = this.$store.state.btnEnables.nciEnables4
      this.nciEnables5 = this.$store.state.btnEnables.nciEnables5
      this.nciEnables6 = this.$store.state.btnEnables.nciEnables6
      this.nciEnables7 = this.$store.state.btnEnables.nciEnables7
      this.nciEnables8 = this.$store.state.btnEnables.nciEnables8
      this.nciEnables9 = this.$store.state.btnEnables.nciEnables9
    },

    saveButtonsConfig () {
      this.sendConfigNv({ nvName: 'nciConfig[4]', nvValue: this.nciConfig4 })
    },

    sendConfigNv (nvData) {
      var msg = {
        type: 'setNetvarValue',
        node: this.node,
        nvName: nvData.nvName,
        value: nvData.nvValue
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    processReplay () {
      switch (this.$store.state.replayData.NvName) {
        case 'nciConfig[4]': this.sendConfigNv({nvName: 'nciConfig[6]', nvValue: this.nciConfig6}); break
        case 'nciConfig[6]': this.sendConfigNv({nvName: 'nciEnables[4]', nvValue: this.nciEnables4 ? 51201 : 0}); break
        case 'nciEnables[4]': this.sendConfigNv({nvName: 'nciEnables[5]', nvValue: this.nciEnables5 ? 51201 : 0}); break
        case 'nciEnables[5]': this.sendConfigNv({nvName: 'nciEnables[6]', nvValue: this.nciEnables6 ? 51201 : 0}); break
        case 'nciEnables[6]': this.sendConfigNv({nvName: 'nciEnables[7]', nvValue: this.nciEnables7 ? 51201 : 0}); break
        case 'nciEnables[7]': this.sendConfigNv({nvName: 'nciEnables[8]', nvValue: this.nciEnables8 ? 51201 : 0}); break
        case 'nciEnables[8]': this.sendConfigNv({nvName: 'nciEnables[9]', nvValue: this.nciEnables9 ? 51201 : 0}); break
        case 'nciEnables[9]': this.sendConfigNv({nvName: 'nciEnables[1]', nvValue: this.nciEnables1 ? 51201 : 0}); break
        case 'nciEnables[1]': this.sendConfigNv({nvName: 'nciEnables[0]', nvValue: this.nciEnables0 ? 51201 : 0}); break
        case 'nciEnables[0]': this.getCfgPackNvs()
      }
    }
  },

  computed: {
    inlineFieldState () {
      if (this.node) return 'inline field'
      return 'inline disabled field'
    },

    fieldState () {
      if (this.node) return 'field'
      return 'disabled field'
    },

    primaryBtnState () {
      if (this.node) return 'ui primary basic button'
      return 'ui disabled button'
    }
  },

  watch: {
    'node': 'getCfgPackNvs',
    '$store.state.btnEnables.nciConfig4': () => { this.nciConfig4 = this.$store.state.btnEnables.nciConfig4 },
    '$store.state.btnEnables.nciConfig6': () => { this.nciConfig6 = this.$store.state.btnEnables.nciConfig6 },
    '$store.state.btnEnables.nciEnables4': () => { this.nciEnables4 = this.$store.state.btnEnables.nciEnables4 },
    '$store.state.btnEnables.nciEnables5': () => { this.nciEnables5 = this.$store.state.btnEnables.nciEnables5 },
    '$store.state.btnEnables.nciEnables6': () => { this.nciEnables6 = this.$store.state.btnEnables.nciEnables6 },
    '$store.state.btnEnables.nciEnables7': () => { this.nciEnables7 = this.$store.state.btnEnables.nciEnables7 },
    '$store.state.btnEnables.nciEnables8': () => { this.nciEnables8 = this.$store.state.btnEnables.nciEnables8 },
    '$store.state.btnEnables.nciEnables9': () => { this.nciEnables9 = this.$store.state.btnEnables.nciEnables9 },
    '$store.state.btnEnables.nciEnables1': () => { this.nciEnables1 = this.$store.state.btnEnables.nciEnables1 },
    '$store.state.btnEnables.nciEnables0': () => { this.nciEnables0 = this.$store.state.btnEnables.nciEnables0 },
    '$store.state.replies': 'processReplay',
    '$store.state.db.devices': 'devicesArrived'
  }
}
</script>

<style>

.hiligthed {
  font-variant: small-caps;
  font-weight: bold;
}

.tempfont {
  font-size: large;
  font-weight: bold;
}

</style>



