<template>
  <div class="ui basic modal">
    <div class="ui icon yellow header">
      <i class="warning circle icon"></i>
      {{$store.state.error.type}}
    </div>
    <div class="content">
      <p style="font-size: larger; text-align: center">{{$store.state.error.message}}</p>
    </div>
    <div class="actions">
      <div class="ui grid">
        <div class="center aligned column">
          <div class="ui button" @click="reload()"><i class="refresh icon"></i> Volver a intentar</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {

  mounted () {
    window.$('.ui.basic.modal').modal({ closable: false }).modal('show')
  },

  methods: {
    reload () {
      document.location.replace(document.location.origin)
    }
  }
}
</script>



