<template>
  <div v-bind:style="pirPosition">
    <div class="ui image">
      <img :src="pirImage" :draggable="$store.state.mapSetup.movable" :style="imgSize" v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      imgSize: { width: '32px' }
    }
  },

  created () {
    this.updatePirSize()
  },

  methods: {
    updatePirSize () {
      this.imgSize.width = this.$store.state.pir.pirXsize + 'px'
    },

    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: 'pir' })
    }
  },

  computed: mapGetters([ 'pirImage', 'pirPosition' ]),

  watch: {
    '$store.state.pir.pirXsize': 'updatePirSize'
  }
}
</script>



