<template>
  <div v-bind:style="setpntPosition" class="ui message" :draggable="$store.state.mapSetup.movable" v-on:dragstart="drag">
    <form class="ui form">
      <table class="ui celled table">
        <tbody>
          <tr>
            <td>
              <div class="ui fluid blue horizontal label">SET POINT</div>
              <div class="ui clearing divider"></div>
              <input type="range" :min="minSetPoint" :max="maxSetPoint" step="0.5" v-model="setPoint.setpntValue">
              <div class="ui clearing divider"></div>
              <div class="ui right labeled button" tabindex="0">
                <div class="ui teal button" @click="sendSetPoint">SET</div>
                <div class="ui basic teal left pointing label">{{setPoint.setpntValue}}</div>
              </div>
            </td>
            <td>
              <div class="ui fluid blue horizontal label">AHORRO ENERGETICO</div>
              <div class="ui clearing divider"></div>
              <div class="ui toggle checkbox">
                <input type="checkbox" name="energyMdOnOff" v-model="nviEnergyHoldOff" @change="energyModeChanged">
                <label>Modo</label>
              </div>
              <div class="ui clearing divider"></div>
              <div class="ui fluid teal horizontal label">{{ecoMode}}</div>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="ui fluid blue horizontal label">MODO DE FUNCIONAMIENTO</div>
              <div class="grouped fields">
                <div class="inline fields" style="margin-top: 1em">
                  <input class="input" type="radio" name="h_Mode" id="heatMode_1" value=1 v-model="nviApplicMode" @change="appModeChanged">
                  <label for="heatMode_1">Calor</label>
                  <input class="input" type="radio" name="c_Mode" id="coolMode_3" value=3 v-model="nviApplicMode" @change="appModeChanged">
                  <label for="coolMode_3">Frio</label>
                  <input class="input" type="radio" name="f_Mode" id="fanMode_9" value=9 v-model="nviApplicMode" @change="appModeChanged">
                  <label for="fanMode_9">Ventilador</label>
                  <input class="input" type="radio" name="a_Mode" id="autoMode_E" value=14 v-model="nviApplicMode" @change="appModeChanged">
                  <label for="autoMode_E">Auto</label>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="ui fluid blue horizontal label">VELOCIDAD DEL VENTILADOR</div>
              <div class="grouped fields">
                <div class="inline fields" style="margin-top: 1em">
                  <input class="input" type="radio" name="fan_v1" id="fan_66" value=66 v-model="nviFanSpeedCmd" @change="fanSpeedChanged">
                  <label for="fan_66">Veloc.1</label>
                  <input class="input" type="radio" name="fan_v2" id="fan_132" value=132 v-model="nviFanSpeedCmd" @change="fanSpeedChanged">
                  <label for="fan_132">Veloc.2</label>
                  <input class="input" type="radio" name="fan_v3" id="fan_200" value=200 v-model="nviFanSpeedCmd" @change="fanSpeedChanged">
                  <label for="fan_200">Veloc.3</label>
                  <input class="input" type="radio" name="fan_a0" id="fan_Auto" value=0 v-model="nviFanSpeedCmd" @change="fanSpeedChanged">
                  <label for="fan_Auto">Auto</label>
                </div>
              </div>
            </td>
          </tr>
          <tr>
            <td colspan="2">
              <div class="ui fluid blue horizontal label">NIVEL DE OCUPACION</div>
              <div class="grouped fields">
                <div class="inline fields" style="margin-top: 1em">
                  <input class="input" type="radio" name="ocup" id="oc_0" value=0 v-model="nviOccCmd" @change="occupChanged">
                  <label for="oc_0">CONFORT</label>
                  <input class="input" type="radio" name="desocup" id="oc_3" value=3 v-model="nviOccCmd" @change="occupChanged">
                  <label for="oc_3">DESOCUPADO</label>
                  <input class="input" type="radio" name="standby" id="oc_1" value=1 v-model="nviOccCmd" @change="occupChanged">
                  <label for="oc_1">STANDBY</label>
                </div>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </form>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data () {
    return {
      setPoint: {
        setpntValue: this.$store.state.alarms.nciTemperaturas9 / 100
      },
      nviEnergyHoldOff: 0,
      nviApplicMode: '',
      nviFanSpeedCmd: '',
      nviOccCmd: ''
    }
  },

  methods: {
    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, imgData: this.setPoint, name: 'setpoint' })
    },

    sendSetPoint () {
      var nrSplited = this.setPoint.setpntValue.toString().split('.')
      var intPart = nrSplited[0]
      var frcPart = nrSplited.length > 1 ? nrSplited[1] : '00'
      if (frcPart.length === 1) frcPart += '0'
      var temp = parseInt(intPart + frcPart)

      this.setNvValue({nvName: 'nviSetPoint', value: temp})
    },

    energyModeChanged () {
      this.setNvValue({
        nvName: 'nviEnergyHoldOff',
        value: this.nviEnergyHoldOff ? 51201 : 0
      })
    },

    appModeChanged () {
      this.setNvValue({
        nvName: 'nviApplicMode',
        value: this.nviApplicMode
      })
    },

    fanSpeedChanged () {
      var speed = 51455
      if (this.nviFanSpeedCmd) {
        speed = this.nviFanSpeedCmd << 8
        speed++
      }

      this.setNvValue({
        nvName: 'nviFanSpeedCmd',
        value: speed
      })
    },

    occupChanged () {
      this.setNvValue({
        nvName: 'nviOccCmd',
        value: this.nviOccCmd
      })
    },

    setNvValue (nvData) {
      var msg = {
        type: 'setNetvarValue',
        node: this.$store.state.device.node,
        nvName: nvData.nvName,
        value: nvData.value
      }
      this.$store.commit('SEND_MESSAGE', msg)
    }
  },

  computed: {
    ...mapGetters([ 'minSetPoint', 'maxSetPoint', 'setpntPosition' ]),

    ecoMode () {
      return this.nviEnergyHoldOff ? 'Ahorro Energtico' : 'Regulando Temperatura'
    }
  },

  watch: {
    '$store.state.generalInfo.ecoMode': function () {
      this.nviEnergyHoldOff = this.$store.state.generalInfo.ecoMode
    },
    '$store.state.generalInfo.unitMode': function () {
      this.nviApplicMode = this.$store.state.generalInfo.unitMode
    },
    '$store.state.fancoil.fanSpeed': function () {
      this.nviFanSpeedCmd = this.$store.state.fancoil.fanSpeed
    },
    '$store.state.generalInfo.unitState': function () {
      this.nviOccCmd = this.$store.state.generalInfo.unitState
    }
  }
}
</script>



