<template>
  <div v-bind:style="winPosition">
    <div class="ui image">
      <img :src="winImage" :draggable="$store.state.mapSetup.movable" :style="imgSize" v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      imgSize: { width: '240px' }
    }
  },

  created () {
    this.updateWinSize()
  },

  methods: {
    updateWinSize () {
      this.imgSize.width = this.$store.state.accesses.winXsize + 'px'
    },

    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: 'window' })
    }
  },

  computed: mapGetters([ 'winImage', 'winPosition' ]),

  watch: {
    '$store.state.accesses.winXsize': 'updateWinSize'
  }
}
</script>



