// App Status
export const onlineImage = state => state.lonNetwork.netOnline ? '../static/images/netOnline.png' : '../static/images/netFail.png'
export const connectStatus = state => state.lonNetwork.netOnline ? 'Red Lonwork Lista!' : 'Red Lonwork Desconectada'

// SetPoint
export const maxSetPoint = state => state.alarms.nciTemperaturas8 / 100
export const minSetPoint = state => state.alarms.nciTemperaturas9 / 100
export const setpntPosition = state => { return { position: 'absolute', left: state.setPoint.setpntXpos + 'px', top: state.setPoint.setpntYpos + 'px' } }

// Temperatures
export const setpntValue = state => state.temperatures.setpntValue / 100
export const roomValue = state => state.temperatures.roomValue / 100
export const nvoAlrDefTemp = state => state.temperatures.nvoAlrDefTemp
export const nvoAlrExcTemp = state => state.temperatures.nvoAlrExcTemp
export const tempPosition = state => { return { position: 'absolute', left: state.temperatures.consigneXpos + 'px', top: state.temperatures.consigneYpos + 'px' } }

// Fancoil
export const fanSpeedImage = function (state) {
  switch (state.fancoil.fanSpeed) {
    case 66: return '../static/images/FanSpeed1.gif'
    case 132: return '../static/images/FanSpeed2.gif'
    case 200: return '../static/images/FanSpeed3.gif'
    default: return '../static/images/FanStop.png'
  }
}
export const fanPosition = state => { return { position: 'absolute', left: state.fancoil.fanXpos + 'px', top: state.fancoil.fanYpos + 'px' } }
export const coolValveImage = state => state.fancoil.coolValve === 0 ? '../static/images/CoolValveClosed.png' : '../static/images/CoolValveOpened.gif'
export const coolValvePosition = state => { return { position: 'absolute', left: state.fancoil.coolValveXpos + 'px', top: state.fancoil.coolValveYpos + 'px' } }
export const heatValveImage = state => state.fancoil.heatValve === 0 ? '../static/images/HeatValveClosed.png' : '../static/images/HeatValveOpened.gif'
export const heatValvePosition = state => { return { position: 'absolute', left: state.fancoil.heatValveXpos + 'px', top: state.fancoil.heatValveYpos + 'px' } }

// CardSlot
export const cardslotImage = state => state.cardSlot.slotStatus === 0 ? '../static/images/CardslotEmpty.png' : '../static/images/CardSlotUsed.png'
export const cardslotPosition = state => { return { position: 'absolute', left: state.cardSlot.slotXpos + 'px', top: state.cardSlot.slotYpos + 'px' } }

// Accesses
export const doorImage = state => state.accesses.doorStatus === 0 ? '../static/images/DoorOpen.png' : '../static/images/DoorClose.png'
export const doorPosition = state => { return { position: 'absolute', left: state.accesses.doorXpos + 'px', top: state.accesses.doorYpos + 'px' } }
export const winImage = state => state.accesses.winStatus === 0 ? '../static/images/WindowOpen.png' : '../static/images/WindowClose.png'
export const winPosition = state => { return { position: 'absolute', left: state.accesses.winXpos + 'px', top: state.accesses.winYpos + 'px' } }

// PIR
export const pirImage = state => state.pir.pirStatus === 0 ? '../static/images/LightON.png' : '../static/images/LightOFF.png'
export const pirPosition = state => { return { position: 'absolute', left: state.pir.pirXpos + 'px', top: state.pir.pirYpos + 'px' } }

// General Info
export const tbInfoPosition = state => { return { position: 'absolute', left: state.generalInfo.tbXpos + 'px', top: state.generalInfo.tbYpos + 'px' } }
export const unitStateImage = state => state.generalInfo.unitState === 0 ? '../static/images/occupied.png' : '../static/images/unoccupied.png'
export const unitModeImage = (state) => {
  switch (state.generalInfo.unitMode) {
    case 1: return '../static/images/heaticon.png'
    case 3: return '../static/images/coolicon.png'
    case 9: return '../static/images/FanStop.png'
    case 14: return '../static/images/autoicon.png'
  }
}
export const ecoModeImg = state => state.generalInfo.ecoMode === 0 ? 'ui large dashboard icon' : 'ui large power icon'

// Message Line & EventLog
export const msgCode = state => state.msgLine.msgCode
export const eventsCount = state => state.events.length



