<template>
  <div class="ui raised left aligned segment" :class="color">
    <div class="ui large blue ribbon label">{{title}}</div>
    <h5 class="ui horizontal divider"><i class="edit icon"></i></h5>
    <form class="ui form"  @submit.prevent="saveNetVar">
      <table class="ui basic table">
        <thead><tr><th colspan="2">{{edtTitle}}</th></tr></thead>
        <tbody>
          <tr>
            <td class="right aligned">Nodo (0..127)</td>
            <td>
              <div class="ui input field">
                <input type="number" v-model="editedNv.device" placeholder="Nodo" min="0" max="127" required>
              </div>
              <label style="margin-left: 20px;">{{selDevName}}</label>
            </td>
          </tr>
          <tr>
            <td class="right aligned">Nombre</td>
            <td>
              <div class="ui input field">
                <input type="text" placeholder="Variable de red" v-model="editedNv.nvName" required>
              </div>
            </td>
          </tr>
          <tr>
            <td class="right aligned">Tipo</td>
            <td>
              <div class="ui field">
                <select v-model="editedNv.type">
                  <option v-for="(type, idx) in nvTypes" :key="idx">{{type}}</option>
                </select>
              </div>
            </td>
          </tr>
          <tr>
            <td class="right aligned">Indice</td>
            <td>
              <div class="ui input field">
                <input type="number" v-model="editedNv.nvIndex" placeholder="NV index" min="0" max="65535" required>
              </div>
            </td>
          </tr>
          <tr>
            <td class="right aligned">Selector</td>
            <td>
              <div class="ui input field">
                <input type="number" v-model="editedNv.selector" placeholder="NV selector" min="0" max="65535" required>
              </div>
            </td>
          </tr>
        </tbody>
        <tfoot class="full-width">
          <tr>
            <th colspan="2">
              <div class="ui right floated small negative labeled icon button" @click="go_back">
                <i class="times icon"></i>Cancelar
              </div>
              <button type="submit" :class="btnSave"><i class="save outline icon"></i>Guardar</button>
            </th>
          </tr>
        </tfoot>
      </table>
    </form>
  </div>
</template>

<script>
export default {
  props: ['edtNv', 'title', 'devices'],

  data () {
    return {
      color: 'yellow',
      edtTitle: '',
      editedNv: {
        id: 0,
        device: 0,
        nvName: '',
        nvIndex: 0,
        selector: 0,
        type: ''
      },
      nvTypes: [
        'SNVT_hvac_mode',
        'SNVT_lev_percent',
        'SNVT_Occupancy',
        'SNVT_Switch',
        'SNVT_Temp_p',
        'SNVT_time_sec',
        'UCPTswitch',
        'UCPTtemp_p',
        'UCPTUINT'
      ]
    }
  },

  created () {
    this.edtTitle = this.edtNv.id ? 'Modificando: ' + this.edtNv.nvName : 'Creando nueva variable de red ...'
    this.editedNv.id = this.edtNv.id
    this.editedNv.device = this.edtNv.device
    this.editedNv.nvName = this.edtNv.nvName
    this.editedNv.nvIndex = this.edtNv.nvIndex
    this.editedNv.selector = this.edtNv.selector
    this.editedNv.type = this.edtNv.type
  },

  computed: {

    selDevName () {
      var dev = this.devices.find((dv) => dv.node === this.editedNv.device)
      return dev ? dev.name : ' ... No Existe!'
    },

    btnSave () {
      var dev = this.devices.find((dv) => dv.node === this.editedNv.device)

      if (!dev) return 'ui small disabled labeled icon button'
      if (!this.editedNv.nvName) return 'ui small disabled labeled icon button'
      if (this.editedNv.nvIndex === '') return 'ui small disabled labeled icon button'
      if (this.editedNv.selector === '') return 'ui small disabled labeled icon button'
      if (!this.editedNv.type) return 'ui small disabled labeled icon button'
      return 'ui small primary labeled icon button'
    }
  },

  methods: {
    saveNetVar () {
      var msg = (this.editedNv.id) ? {
        type: 'db.update',
        tbName: 'nvdata',
        sets: `device=${this.editedNv.device},nvName='${this.editedNv.nvName}',nvIndex=${this.editedNv.nvIndex},selector=${this.editedNv.selector},type='${this.editedNv.type}'`,
        whr: `id=${this.editedNv.id}`
      } : {
        type: 'db.insert',
        tbName: 'nvdata',
        fields: 'device,nvName,nvIndex,selector,type',
        values: `${this.editedNv.device},'${this.editedNv.nvName}',${this.editedNv.nvIndex},${this.editedNv.selector},'${this.editedNv.type}'`
      }
      this.$store.commit('SEND_MESSAGE', msg)
      if (this.editedNv.id) {
        this.$emit('go_back', {
          id: this.editedNv.id,
          device: this.editedNv.device,
          nvName: this.editedNv.nvName,
          nvIndex: this.editedNv.nvIndex,
          selector: this.editedNv.selector,
          type: this.editedNv.type,
          selected: false
        })
      }
    },

    insertedCallback () {
      if (this.$store.state.db.inserted && this.$store.state.db.inserted.tbName === 'nvdata') {
        this.$emit('go_back', {
          id: this.$store.state.db.inserted.tbName.id,
          device: this.editedNv.device,
          nvName: this.editedNv.nvName,
          nvIndex: this.editedNv.nvIndex,
          selector: this.editedNv.selector,
          type: this.editedNv.type,
          selected: false
        })
        this.$store.commit('SET_INSERTED_CALLBACK', null)
      }
    },

    go_back () { this.$emit('go_back', null) }
  },

  watch: {
    '$store.state.db.inserted': 'insertedCallback'
  }
}
</script>



