<template>
  <div v-bind:style="valve.type == 'heatValve' ? heatValvePosition : coolValvePosition">
    <div class="ui image">
      <img :src="valve.type == 'heatValve' ? heatValveImage : coolValveImage"
        :draggable="$store.state.mapSetup.movable"
        :style="imgSize"
        v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  props: ['valve'],

  data () {
    return {
      imgSize: { width: '50px' }
    }
  },

  created () {
    this.updateValveSize()
  },

  methods: {
    updateValveSize () {
      this.imgSize.width = this.valve.type === 'heatValve' ? this.$store.state.fancoil.heatValveXsize + 'px' : this.$store.state.fancoil.coolValveXsize + 'px'
    },

    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: this.valve.type })
    }
  },

  computed: mapGetters([ 'coolValvePosition', 'heatValvePosition', 'coolValveImage', 'heatValveImage' ]),

  watch: {
    '$store.state.fancoil.heatValveXsize': 'updateValveSize',
    '$store.state.fancoil.coolValveXsize': 'updateValveSize'
  }
}
</script>



