<template>
  <div :style="msgPosition">
    <div :class="msgCode == 1 ? 'ui red tag label' : 'ui black tag label'">{{$store.state.msgLine.message}}</div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      msgPosition: {
        position: 'fixed',
        bottom: '0',
        left: '310px'
      }
    }
  },

  computed: mapGetters([ 'msgCode' ])
}
</script>



