<template>
  <div class="ui attached stackable menu">
    <div class="ui container">
      <div class="item"><img :src="onlineImage"></div>
        <div class="header item">
          <i :class="moduleIcon"></i>
          <span style="margin-left:10px">{{moduleName}}</span>
        </div>
        <div class="right menu">
          <div class="ui dropdown item">
            <i class="options icon"></i> Configuraci&oacute;n
            <div class="menu">
              <div class="item">
                <router-link class="item" :to="{name: 'lonSetup'}">
                  <i class="sitemap icon"></i>Conexi&oacute;n a Red Lonwork
                </router-link>
                <router-link class="item" :to="{name: 'lonNodes'}">
                  <i class="microchip icon"></i>Nodos y Variables de Red
                </router-link>
                <router-link class="item" :to="{name: 'thermostat'}">
                  <i class="tasks icon"></i>Termostato. Qu&eacute; Controla?
                </router-link>
                <router-link class="item" :to="{name: 'thrbuttons'}">
                  <i class="keyboard outline icon"></i>Termostato. Botones
                </router-link>
                <router-link class="item" :to="{name: 'accesses'}">
                  <i class="lightbulb icon"></i>Estados y Modos de Operaci&oacute;n
                </router-link>
                <router-link class="item" :to="{name: 'alarms'}">
                  <i class="bell outline icon"></i>Auto-regulaci&oacute;n y Alarmas
                </router-link>
              </div>
            </div>
          </div>
          <div v-if="isHome" class="item">
            <i class="large link icons" @click="toggleDragging">
              <i class="move icon"></i>
              <i :class="$store.state.mapSetup.movable ? '' : 'large red dont icon'"></i>
            </i>
          </div>
          <a v-else class="item" @click="goHome"><i class="home large icon"></i></a>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      ready: false
    }
  },

  mounted () {
    window.$('.ui.dropdown').dropdown()
    this.queryInitialData()
    this.goHome()
  },

  methods: {
    goHome () {
      this.$router.replace({name: 'home'})
    },

    toggleDragging () {
      this.$store.commit('TOGGLE_MOVABLE')
    },

    getFirstNode () {
      if ((this.$store.state.device.id === 0) && this.$store.state.connected) {
        var msg = {
          type: 'db.selectOne',
          dbName: 'devices',
          whr: 'node <> 0 LIMIT 1'
        }
        this.$store.commit('SEND_MESSAGE', msg)
      }
    },

    queryInitialData () {
      if (this.$store.state.lonNetwork.netOnline && this.$store.state.device.ready) {
        this.getNvPackValues({
          node: this.$store.state.device.node,
          nvPack: 'nvoFanSpeed,nvoEffectSetPt,nvoHVACtemp,nvoSwitch_1,nvoSwitch_2,nvoSwitch_3,nvoCoolOutput,nvoEnergyHoldOff,nvoOccCmd,nvoApplicMode,nciTemperaturas[8],nciTemperaturas[9],nvoOccup'
        })
      }
    },

    getNvPackValues (msgData) {
      var msg = {
        type: 'getNvPackValues',
        node: msgData.node,
        nvPack: msgData.nvPack
      }
      this.$store.commit('SEND_MESSAGE', msg)
    }
  },

  computed: {
    ...mapGetters([ 'onlineImage' ]),

    isHome () { return this.$route.name === 'home' },

    moduleName () {
      var routeName = this.$route.name
      switch (routeName) {
        case 'home': return 'DEMO DEL TERMOSTATO IT100-PIR'
        case 'lonSetup': return 'CONEXION A LA RED LONWORKS'
        case 'lonNodes': return 'NODOS Y VARIABLES DE RED'
        case 'thermostat': return 'CONFIGURACION DEL TERMOSTATO'
        case 'thrbuttons': return 'TERMOSTATO - HABILITAR/DESHABILITAR BOTONES'
        case 'accesses': return 'ESTADOS Y MODOS DE OPERACION'
        case 'alarms': return 'AUTO-REGULACION Y ALARMAS'
        default: return 'Sistema de Control de Temperatura'
      }
    },

    moduleIcon () {
      switch (this.$route.name) {
        case 'home': return 'text height large fitted icon'
        case 'lonSetup': return 'sitemap large fitted icon'
        case 'lonNodes': return 'microchip large fitted icon'
        case 'thermostat': return 'tasks large fitted icon'
        case 'thrbuttons': return 'keyboard outline large fitted icon'
        case 'accesses': return 'lightbulb large fitted icon'
        case 'alarms': return 'bell outline large fitted icon'
        default: return 'scribd large fitted icon'
      }
    }
  },

  watch: {
    '$store.state.connected': 'getFirstNode',
    '$store.state.lonNetwork.netOnline': 'queryInitialData',
    '$store.state.device.ready': 'queryInitialData'
  }
}
</script>



