export const SET_ERROR_STATE = (state, err) => {
  state.error.active = err.active
  state.error.type = err.type
  state.error.message = err.message
}
export const SET_ERROR_ACTIVE_STATE = (state, value) => { state.error.active = value }
export const INITIALIZE_DEV_STATUS = (state) => {
  state.fancoil.fanSpeed = 0
  state.fancoil.coolValve = 0
  state.fancoil.heatValve = 0
  state.accesses.doorStatus = 100
  state.accesses.winStatus = 100
  state.cardSlot.slotStatus = 0
  state.generalInfo.ecoMode = -1
  state.generalInfo.unitMode = ''
  state.generalInfo.unitState = ''
  state.temperatures.setpntValue = 0
  state.temperatures.roomValue = 0
  state.setPoint.setpntValue = 18.0
  state.pir.pirStatus = 1
}

export const SAVE_DEVICE_DATA = (state, data) => {
  state.device.id = data.id
  state.device.node = data.node
  state.device.nid = data.nid
  state.device.ready = true
}

export const SET_CONNECTED_STATE = (state, value) => { state.connected = value }

export const TOGGLE_MOVABLE = (state) => { state.mapSetup.movable = !state.mapSetup.movable }

