export const SET_MESSAGE_LINE = (state, data) => {
  state.events.unshift({ msg: data.msg })
  state.msgLine.msgCode = data.code
  state.msgLine.message = data.msg
  if (data.code === 0xFF) state.connected = true
}

export const SET_LON_NETWORK_DATA = (state, data) => {
  state.lonNetwork.currNi = data.currNi
  state.lonNetwork.niList = data.niList
  state.lonNetwork.domId = data.domId
  state.lonNetwork.domIdLen = data.domIdLen
  state.lonNetwork.node = data.node
  state.lonNetwork.subnet = data.subnet
  state.lonNetwork.netOnline = data.currNi !== ''
}
export const SET_FOUND_DEVICES = (state, data) => { state.lonNetwork.foundDevices = data }

export const SET_RX_REPLY_DATA = (state, data) => {
  state.replayData.Node = data.Node
  state.replayData.NvName = data.NvName
  state.replies++
}

export const SEND_MESSAGE = (state, msg) => { state.serverMsg = msg ? JSON.stringify(msg) : '' }

export const SET_DEVICES_FROM_DB = (state, data) => { state.db.devices = data }
export const SET_NETVARS_FROM_DB = (state, data) => { state.db.netVars = data }
export const SET_INSERTED_CALLBACK = (state, data) => { state.db.inserted = data }



