<template>
  <div style="position: fixed; bottom: 0; width: 100%">
    <button @click="active = !active" class="ui secondary tiny button">
      <i class="laptop icon"></i> Reporte de eventos
      <a v-if="eventsCount" class="ui red tiny label">{{eventsCount}}</a>
      <i :class="{'angle double up icon': !active, 'angle double down icon': active }"></i>
    </button>
    <div style="max-height: 200px; overflow-y: auto">
      <div v-if="active && eventsCount">
        <table class="ui compact tiny segment table">
          <tbody>
            <tr v-for="(event, i) in $store.state.events" :key="i">
              <td>{{i + 1}}</td>
              <td>{{event.msg}}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div v-if="active && !eventsCount" class="ui segment">
        <p>No hay eventos.</p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      active: false
    }
  },

  computed: mapGetters([ 'eventsCount' ])
}
</script>



