import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/IT-Home.vue'
import lonSetup from '../components/Setup-LonNetwork.vue'
import lonNodes from '../components/Setup-LonNodes.vue'
import thermostat from '../components/Setup-Thermostat.vue'
import thrbuttons from '../components/Setup-Therm-Buttons.vue'
import accesses from '../components/Setup-Accesses.vue'
import alarms from '../components/Setup-Alarms.vue'

Vue.use(Router)

const routes = [
  { path: '/home', name: 'home', component: home },
  { path: '/lonSetup', name: 'lonSetup', component: lonSetup },
  { path: '/lonNodes', name: 'lonNodes', component: lonNodes },
  { path: '/thermostat', name: 'thermostat', component: thermostat },
  { path: '/thrbuttons', name: 'thrbuttons', component: thrbuttons },
  { path: '/accesses', name: 'accesses', component: accesses },
  { path: '/alarms', name: 'alarms', component: alarms }
]
const router = new Router({ mode: 'history', routes: routes })

export default router



