<template>
  <div id="app" class="ui fluid container" style="height: 100%">
    <navbar></navbar>
    <router-view class="animated fadeIn"></router-view>
    <messageLine></messageLine>
    <eventLog></eventLog>
    <modal v-if="$store.state.error.active"></modal>
    <connect :url="url" @set_error="set_error" @msg_sent="msg_sent"></connect>
  </div>
</template>

<script>
import messageLine from './components/IT-Message-Line.vue'
import eventLog from './components/IT-EventLog.vue'
import modal from './components/IT_Modal_Error.vue'
import navbar from './components/IT-Navbar.vue'
import connect from './components/ServerConnect.vue'

var wsProtocol = document.location.protocol === 'https:' ? 'wss' : 'ws'
var port = document.location.protocol === 'https:' ? 24072 : 24072

export default {
  name: 'App',

  data () {
    return {
      url: {
        wsUrl: `${wsProtocol}://${document.location.hostname}:${port}/ws`,
        baseUrl: 'api/',
        myParent: this
      },
      loggedUser: {
        userId: 1,
        userName: 'Administrador',
        userPhoto: '',
        userReadOnly: true
      }
    }
  },

  created () {
    // var userId = window.$('#user_id').attr('value')
    // this.loggedUser.userId = parseInt(userId)
    // this.loggedUser.userName = window.$('#user_name').attr('value')
    // var photo = window.$('#user_photo').attr('value')
    // this.loggedUser.userPhoto = photo ? `fotos/${photo}` : ''
  },

  methods: {

    set_error (err) {
      this.$store.commit('SET_ERROR_STATE', err)
      this.$store.commit('SET_CONNECTED_STATE', err.connected)
    },

    msg_sent (msg) {
      this.$store.commit('SEND_MESSAGE', '')
    }
  },

  components: { navbar, connect, messageLine, eventLog, modal }
}
</script>

<style>
</style>



