<template>
  <div v-bind:style="tempPosition" class="ui message" :draggable="$store.state.mapSetup.movable" v-on:dragstart="drag">
    <div class="ui fluid blue horizontal label">TEMPERATURAS</div>
    <table class="ui collapsing celled table">
      <tbody>
        <tr>
          <td><div class="ui center aligned header">T. Consigna</div></td>
          <td><div class="ui center aligned huge header">{{setpntValue}} <sup>o</sup>C</div></td>
        </tr>
        <tr>
          <td><div class="ui center aligned header">T. Ambiente</div></td>
          <td><div class="ui center aligned huge header">{{roomValue}} <sup>o</sup>C</div></td>
        </tr>
        <tr v-if="nvoAlrDefTemp != 0">
          <td colspan="2"><i class="ui large blue snowflake icon"></i><span class="caps">Muy Baja</span></td>
        </tr>
        <tr v-if="nvoAlrExcTemp != 0">
          <td colspan="2"><i class="ui large red sun icon"></i><span class="caps">Muy Alta</span></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  methods: {
    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, imgData: this.temperatures, name: 'temperatures' })
    }
  },

  computed: mapGetters([ 'setpntValue', 'roomValue', 'tempPosition', 'nvoAlrDefTemp', 'nvoAlrExcTemp' ])
}
</script>

<style>

.caps {
  font-variant: small-caps;
  font-weight: bold;
}

</style>



