<template>
  <div class="ui left aligned raised container segment" :class="color">
    <div class="ui pointing menu">
      <div class="ui container">
        <div class="item">
          <select class="ui dropdown" v-model="node">
            <option v-for="(dev, idx) in devices" :value="dev.node" :key="idx">N{{dev.node}} - {{dev.name}}</option>
          </select>
        </div>
        <div class="item">
          <button :class="primaryBtnState" @click="refreshCurrentValues">
            <i class="ui upload icon"></i>
          Traer Confg. Actual
          </button>
        </div>
        <div class="right menu">
          <div class="item">
            <button :class="primaryBtnState" @click="saveModesConfig">
              <i class="ui download icon"></i>
              Enviar Confg. al Nodo
            </button>
          </div>
        </div>
      </div>
    </div>
    <form class="ui form">
      <table class="ui basic table">
        <tbody>
          <tr>
            <td rowspan="2"><img src="../../static/images/cardsloticon.jpg" alt="Casillero"></td>
              <td colspan="2">
                <div :class="inlineFieldState">
                  <input v-model="nciEnables13" class="ui checkbox" type="checkbox">
                  <label>Habilitar monitoreo del Casillero</label>
                </div>
                <div v-if="!nciEnables13" :class="inlineFieldState">
                  <input v-model="nciEnables16" class="ui checkbox" type="checkbox">
                  <label>Habilitar salida para el Contactor de Servicio</label>
                </div>
              </td>
            </tr>
            <tr>
              <td>
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Casillero" v-model="nciTiempos9" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Seg.</label>
                </div>
              </td>
              <td>
                <p>Tiempo en segundos desde que se saca la tarjeta del casillero hasta que la unidad pasa a STANDBY</p>
              </td>
            </tr>
            <tr>
              <td rowspan="2"><img src="../../static/images/winicon.png" alt="Ventana"></td>
              <td colspan="2">
                <div :class="inlineFieldState">
                  <input v-model="nciEnables14" class="ui checkbox" type="checkbox">
                  <label>Habilitar monitoreo de Ventana</label>
                </div>
              </td>
            </tr>
            <tr>
              <td v-if="nciEnables14">
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Ventana" v-model="nciTiempos5" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Seg.</label>
                </div>
              </td>
              <td v-if="nciEnables14">
                <p>Tiempo en segundos que debe permanecer la ventana abierta para que la unidad deje de regular y pase a modo ECONOMICO</p>
              </td>
            </tr>

            <tr>
              <td rowspan="3"><img src="../../static/images/dooricon.png" alt="Puerta"></td>
              <td colspan="2">
                <div :class="inlineFieldState">
                  <input v-model="nciEnables15" class="ui checkbox" type="checkbox">
                  <label>Habilitar monitoreo de Puerta</label>
                </div>
              </td>
            </tr>
            <tr>
              <td v-if="nciEnables15">
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Puerta" v-model="nciTiempos6" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Seg.</label>
                </div>
              </td>
              <td v-if="nciEnables15">
                <p>Tiempo en segundos que debe permanecer la puerta abierta para que la unidad deje de regular y pase a modo ECONOMICO</p>
              </td>
            </tr>
            <tr>
              <td>
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="P y V" v-model="nciTiempos7" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Seg.</label>
                </div>
              </td>
              <td>
                <p>Tiempo en segundos que debe permanecer la PUERTA y VENTANA cerradas para que la unidad salga del modo ECONOMICO y reanude la REGULACION</p>
              </td>
            </tr>

            <tr>
              <td rowspan="3"><img src="../../static/images/piricon.jpg" alt="PIR"></td>
              <td>
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Pir" v-model="nciDebounce" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Seg.</label>
                </div>
              </td>
              <td>
                <p>Tiempo en segundos que dura el estado OCUPACION a partir de que el PIR detecta movimiento, al espirar este tiempo se decreta el estado DESOCUPACION</p>
              </td>
            </tr>
            <tr>
              <td>
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Pir" v-model="nciTiempos10" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Min.</label>
                </div>
              </td>
              <td>
                <p>Tiempo en minutos desde la &uacute;ltima detecci&oacute;n de presencia para que la unidad pase a estado STANDBY</p>
              </td>
            </tr>
            <tr>
              <td>
                <div class="inline fields">
                  <div :class="inputState">
                    <input type="number" placeholder="Standby" v-model="nciTiempos11" min="0" max="65535" required>
                  </div>
                  <label style="margin-left: 10px;">Min.</label>
                </div>
              </td>
              <td>
                <p>Tiempo en minutos que debe permanecer la unidad en STANDBY para pasar al estado DESOCUPACION</p>
              </td>
            </tr>
          </tbody>
        </table>
    </form>
  </div>
</template>

<script>
export default {

  data () {
    return {
      devices: [],
      node: 0,
      nciEnables13: 1,
      nciEnables14: 1,
      nciEnables15: 1,
      nciEnables16: 1,
      nciTiempos5: 0,
      nciTiempos6: 0,
      nciTiempos7: 0,
      nciTiempos9: 0,
      nciTiempos10: 0,
      nciTiempos11: 0,
      nciDebounce: 0
    }
  },

  created () {
    this.refreshCurrentValues()
    this.loadDevices()
  },

  methods: {
    loadDevices () {
      this.color = 'red'
      var msg = { type: 'db.getDevices', queryid: 'CLI-GETDEVICES' }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    devicesArrived () {
      if (!this.$store.state.db.devices.length) return
      this.color = 'green'
      var devices = []
      for (var i = 0; i < this.$store.state.db.devices.length; i++) {
        if (this.$store.state.db.devices[i].node) {
          devices.push(this.$store.state.db.devices[i])
        }
      }
      this.devices = devices
      this.$store.commit('SET_DEVICES_FROM_DB', null)
    },

    getCfgPackNvs () {
      var msg = {
        type: 'getNvPackValues',
        node: this.node,
        nvPack: 'nciEnables[13],nciEnables[14],nciEnables[15],nciEnables[16],nciTiempos[5],nciTiempos[6],nciTiempos[7],nciTiempos[9],nciTiempos[10],nciTiempos[11],nciDebounce'
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    refreshCurrentValues () {
      this.nciEnables13 = this.$store.state.status.nciEnables13
      this.nciEnables14 = this.$store.state.status.nciEnables14
      this.nciEnables15 = this.$store.state.status.nciEnables15
      this.nciEnables16 = this.$store.state.status.nciEnables16
      this.nciTiempos5 = this.$store.state.status.nciTiempos5
      this.nciTiempos6 = this.$store.state.status.nciTiempos6
      this.nciTiempos7 = this.$store.state.status.nciTiempos7
      this.nciTiempos9 = this.$store.state.status.nciTiempos9
      this.nciTiempos10 = this.$store.state.status.nciTiempos10
      this.nciTiempos11 = this.$store.state.status.nciTiempos11
      this.nciDebounce = this.$store.state.status.nciDebounce
    },

    saveModesConfig () {
      this.sendConfigNv({ nvName: 'nciEnables[13]', nvValue: this.nciEnables13 })
    },

    processReplay () {
      switch (this.$store.state.replayData.NvName) {
        case 'nciEnables[13]': this.sendConfigNv({nvName: 'nciEnables[14]', nvValue: this.nciEnables14 ? 51201 : 0}); break
        case 'nciEnables[14]': this.sendConfigNv({nvName: 'nciEnables[15]', nvValue: this.nciEnables15 ? 51201 : 0}); break
        case 'nciEnables[15]': this.sendConfigNv({nvName: 'nciEnables[16]', nvValue: this.nciEnables16 ? 51201 : 0}); break
        case 'nciEnables[16]': this.sendConfigNv({nvName: 'nciTiempos[5]', nvValue: this.nciTiempos5}); break
        case 'nciTiempos[5]': this.sendConfigNv({nvName: 'nciTiempos[6]', nvValue: this.nciTiempos6}); break
        case 'nciTiempos[6]': this.sendConfigNv({nvName: 'nciTiempos[7]', nvValue: this.nciTiempos7}); break
        case 'nciTiempos[7]': this.sendConfigNv({nvName: 'nciTiempos[9]', nvValue: this.nciTiempos9}); break
        case 'nciTiempos[9]': this.sendConfigNv({nvName: 'nciTiempos[10]', nvValue: this.nciTiempos10}); break
        case 'nciTiempos[10]': this.sendConfigNv({nvName: 'nciTiempos[11]', nvValue: this.nciTiempos11}); break
        case 'nciTiempos[11]': this.sendConfigNv({nvName: 'nciDebounce', nvValue: this.nciDebounce}); break
        case 'nciDebounce': this.getCfgPackNvs()
      }
    },

    sendConfigNv (nvData) {
      var msg = {
        type: 'setNetvarValue',
        node: this.node,
        nvName: nvData.nvName,
        value: nvData.nvValue
      }
      this.$store.commit('SEND_MESSAGE', msg)
    }
  },

  computed: {
    inlineFieldState () { return this.node ? 'inline field' : 'inline disabled field' },
    fieldState () { return this.node ? 'field' : 'disabled field' },
    primaryBtnState () { return this.node ? 'ui primary basic button' : 'ui disabled button' },
    inputState () { return this.node ? 'ui input' : 'ui disabled input' }
  },

  watch: {
    'node': 'getCfgPackNvs',
    '$store.state.status.nciEnables13': function () { this.nciEnables13 = this.$store.state.status.nciEnables13 },
    '$store.state.status.nciEnables14': function () { this.nciEnables14 = this.$store.state.status.nciEnables14 },
    '$store.state.status.nciEnables15': function () { this.nciEnables15 = this.$store.state.status.nciEnables15 },
    '$store.state.status.nciEnables16': function () { this.nciEnables16 = this.$store.state.status.nciEnables16 },
    '$store.state.status.nciTiempos5': function () { this.nciTiempos5 = this.$store.state.status.nciTiempos5 },
    '$store.state.status.nciTiempos6': function () { this.nciTiempos6 = this.$store.state.status.nciTiempos6 },
    '$store.state.status.nciTiempos7': function () { this.nciTiempos7 = this.$store.state.status.nciTiempos7 },
    '$store.state.status.nciTiempos9': function () { this.nciTiempos9 = this.$store.state.status.nciTiempos9 },
    '$store.state.status.nciTiempos10': function () { this.nciTiempos10 = this.$store.state.status.nciTiempos10 },
    '$store.state.status.nciTiempos11': function () { this.nciTiempos11 = this.$store.state.status.nciTiempos11 },
    '$store.state.status.nciDebounce': function () { this.nciDebounce = this.$store.state.status.nciDebounce },
    '$store.state.replies': 'processReplay',
    '$store.state.db.devices': 'devicesArrived'
  }
}
</script>



