<template>
  <div style="position: absolute;">
    <div v-on:drop="drop" v-on:dragover="allowDrop" style="height:100%; position:relative;">
      <img style="width:75%;" src="../../static/images/IT100-PIR.png" alt="Foto del Termostato">
      <fancoilFan v-on:reallocate="reallocate"></fancoilFan>
      <fancoilValve v-on:reallocate="reallocate" :valve="{ type:'heatValve' }"></fancoilValve>
      <fancoilValve v-on:reallocate="reallocate" :valve="{ type:'coolValve' }"></fancoilValve>
      <sensorPir v-on:reallocate="reallocate"></sensorPir>
      <accessDoor v-on:reallocate="reallocate"></accessDoor>
      <accessWindow v-on:reallocate="reallocate"></accessWindow>
      <cardSlot v-on:reallocate="reallocate"></cardSlot>
      <unitInfo v-on:reallocate="reallocate"></unitInfo>
      <tempValue v-on:reallocate="reallocate"></tempValue>
      <setPoint v-on:reallocate="reallocate"></setPoint>
    </div>
  </div>
</template>

<script>
import fancoilFan from '../components/IT-Fan-Image.vue'
import fancoilValve from '../components/IT-Valve-Image.vue'
import accessDoor from '../components/IT-Door-Image.vue'
import accessWindow from '../components/IT-Window-Image.vue'
import cardSlot from '../components/IT-Cardslot-Image.vue'
import unitInfo from '../components/IT-Unit-Info.vue'
import tempValue from '../components/IT-Temp-Value.vue'
import setPoint from '../components/IT-Set-Point.vue'
import sensorPir from '../components/IT-Pir-Image.vue'

export default {

  data () {
    return {
      takeLeft: 0,
      takeTop: 0,
      takeImg: {},
      takeName: ''
    }
  },

  methods: {
    loadFancoilData () {
      var msg = {
        type: 'db.selectOne',
        dbName: 'fancoil',
        whr: `device=${this.$store.state.device.id}`
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    getNvValue (msgData) {
      var msg = {
        type: 'getNvValue',
        node: msgData.node,
        nvName: msgData.nvName
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    allowDrop: function (_evt) {
      _evt.preventDefault()
    },

    drop (_evt) {
      _evt.preventDefault()
      var left = _evt.offsetX - this.takeLeft
      var top = _evt.offsetY - this.takeTop
      var data = null
      switch (this.takeName) {
        case 'fan':
          this.$store.commit('SET_FAN_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'fancoil',
            sets: `fanXpos=${left},fanYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'heatValve':
          this.$store.commit('SET_HEAT_VALVE_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'fancoil',
            sets: `heatValveXpos=${left},heatValveYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'coolValve':
          this.$store.commit('SET_COLD_VALVE_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'fancoil',
            sets: `coolValveXpos=${left},coolValveYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'pir':
          this.$store.commit('SET_PIR_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'pirsensors',
            sets: `pirXpos=${left},pirYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'door':
          this.$store.commit('SET_DOOR_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'accesses',
            sets: `doorXpos=${left},doorYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'window':
          this.$store.commit('SET_WINDOW_POSITION', {x: left, y: top})
          data = {
            type: 'db.update',
            tbName: 'accesses',
            sets: `winXpos=${left},winYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'cardSlot':
          this.$store.commit('SET_CARDSLOT_POSITION', { x: left, y: top })
          data = {
            type: 'db.update',
            tbName: 'cardslots',
            sets: `slotXpos=${left},slotYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'generalInfo':
          this.$store.commit('SET_UNITSTATUS_POSITION', { x: left, y: top })
          data = {
            type: 'db.update',
            tbName: 'generaldata',
            sets: `tbXpos=${left},tbYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'temperatures':
          this.$store.commit('SET_TEMP_POSITION', { x: left, y: top })
          data = {
            type: 'db.update',
            tbName: 'temperatures',
            sets: `consigneXpos=${left},consigneYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
        case 'setpoint':
          this.$store.commit('SET_SETPOINT_POSITION', { x: left, y: top })
          data = {
            type: 'db.update',
            tbName: 'setpoints',
            sets: `setpntXpos=${left},setpntYpos=${top}`,
            whr: `device=${this.$store.state.device.id}`
          }
          break
      }
      console.log(data)
      if (data) this.$store.commit('SEND_MESSAGE', data)
    },

    reallocate (picData) {
      this.takeLeft = picData.pic.takeLeft
      this.takeTop = picData.pic.takeTop
      this.takeName = picData.name
      this.reallocating = true
    }
  },

  watch: {
    '$store.state.device.id': 'loadFancoilData'
  },

  components: { fancoilFan, fancoilValve, accessDoor, accessWindow, cardSlot, unitInfo, tempValue, setPoint, sensorPir }
}
</script>



