<template>
  <div v-bind:style="fanPosition">
    <div class="ui image">
      <img :src="fanSpeedImage" :draggable="$store.state.mapSetup.movable" :style="imgSize" v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      imgSize: { width: '100px' }
    }
  },

  created () {
    this.imgSize.width = this.$store.state.fancoil.fanXsize + 'px'
  },

  methods: {
    drag (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: 'fan' })
    }
  },

  computed: mapGetters([ 'fanPosition', 'fanSpeedImage' ])
}
</script>



