<template>
  <div v-bind:style="cardslotPosition">
    <div class="ui image">
      <img :src="cardslotImage" :draggable="$store.state.mapSetup.movable" :style="imgSize" v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      imgSize: { width: '50px' }
    }
  },

  created: function () {
    this.updateSlotSize()
  },

  methods: {
    updateSlotSize: function () {
      this.imgSize.width = this.$store.state.cardSlot.slotXsize + 'px'
    },

    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, imgData: this.cardSlot, name: 'cardSlot' })
    }
  },

  computed: mapGetters([ 'cardslotImage', 'cardslotPosition' ]),

  watch: {
    '$store.state.cardSlot.slotXsize': 'updateSlotSize'
  }
}
</script>



