<template>
  <div></div>
</template>

<script>
export default {
  props: ['url'],

  data () {
    return {
      socket: new WebSocket(this.url.wsUrl),
      mySelf: ''
    }
  },

  created () {
    var mySelf = this

    this.socket.onopen = function (event) {
      console.log('Socket online!')
      mySelf.$emit('set_error', { active: false, type: '', message: 'Conectado...', connected: true })
      mySelf.socket.send(JSON.stringify({ type: 'getLonState', data: 'hrwStatus' }))
    }

    this.socket.onclose = function (event) {
      console.log('Socket Closed')
      mySelf.$emit('set_error', {
        active: true,
        type: 'Error del Servidor',
        message: 'Se ha cerrado la conexin con el Servidor',
        connected: false
      })
    }

    this.socket.onmessage = function (event) {
      var srvEvent = JSON.parse(event.data)

      mySelf.$store.commit('SET_ERROR_ACTIVE_STATE', false)

      switch (srvEvent.type) {
        case 'nv_Value': mySelf.newNvValue(mySelf, srvEvent.data); break
        case 'system_event': mySelf.$root.$emit('system_event', srvEvent.data); break
        case 'network_status': mySelf.setNetworkData(mySelf, srvEvent.data); break
        case 'lonReconnect': mySelf.socket.send(JSON.stringify({ type: 'getLonState', data: 'hrwStatus' })); break
        case 'system_message': mySelf.showMessageLine(mySelf, srvEvent.data); break
        case 'received_nvCommand': mySelf.$store.commit('SET_RX_REPLY_DATA', srvEvent.data); break
        case 'network_scan': mySelf.$store.commit('SET_FOUND_DEVICES', srvEvent.data); break
        case 'db.selectOne': mySelf.dbSelectOne(mySelf, srvEvent.dbName, srvEvent.data); break
        case 'db.getDevices': mySelf.$store.commit('SET_DEVICES_FROM_DB', srvEvent.data); break
        case 'db.getNetVars': mySelf.$store.commit('SET_NETVARS_FROM_DB', srvEvent.data); break
        case 'db.insert': mySelf.$store.commit('SET_INSERTED_CALLBACK', srvEvent.data); break
      }
    }

    this.socket.onerror = function (event) {
      alert('Error de Conexion')
    }
  },

  methods: {

    sendMessage () {
      if (this.$store.state.serverMsg) {
        this.socket.send(this.$store.state.serverMsg)
        this.$emit('msg_sent', null)
      }
    },

    setNetworkData (context, data) {
      context.$store.commit('SET_LON_NETWORK_DATA', data)
      if (!data.currNi) context.$store.commit('INITIALIZE_DEV_STATUS')
    },

    newNvValue (context, data) {
      switch (data.nvName) {
        case 'n1SetPoint':
        case 'nvoEffectSetPt':
          context.$store.commit('SET_SETPOINT_VALUE', data.value)
          break
        case 'n1HVACtemp':
        case 'nvoHVACtemp':
          context.$store.commit('SET_ROOMTEMP_VALUE', data.value)
          break
        case 'n1FanSpeed':
        case 'nvoFanSpeed':
          context.$store.commit('SET_FAN_SPEED_VALUE', data.value >> 8)
          break
        case 'n1CoolOutput':
        case 'nvoCoolOutput':
          context.$store.commit('SET_COLD_VALVE_VALUE', data.value)
          break
        case 'n1HeatOutput':
        case 'nvoHeatOutput':
          context.$store.commit('SET_HEAT_VALVE_VALUE', data.value)
          break
        case 'n1CardSlot':
        case 'nvoSwitch_1':
          context.$store.commit('SET_CARDSLOT_STATUS', data.value)
          break
        case 'n1Window':
        case 'nvoSwitch_2':
          context.$store.commit('SET_WINDOW_STATUS', data.value)
          break
        case 'n1Door':
        case 'nvoSwitch_3':
          context.$store.commit('SET_DOOR_STATUS', data.value)
          break
        case 'n1Occup':
        case 'nvoOccup':
          context.$store.commit('SET_PIR_STATUS', data.value)
          break
        case 'n1EnergyHoldOff':
        case 'nvoEnergyHoldOff':
          context.$store.commit('SET_ECOMODE_STATUS', data.value)
          break
        case 'n1OccCmd':
        case 'nvoOccCmd':
          context.$store.commit('SET_UNITSTATE_STATUS', data.value)
          break
        case 'n1ApplicMode':
        case 'nvoApplicMode':
          context.$store.commit('SET_UNITMODE_STATUS', data.value)
          break
        case 'nciConfig[0]': context.$store.commit('SET_MACH_TYPE', data.value); break
        case 'nciConfig[1]': context.$store.commit('SET_COOLVALVE_TYPE', data.value); break
        case 'nciConfig[2]': context.$store.commit('SET_HEATVALVE_TYPE', data.value); break
        case 'nciConfig[3]': context.$store.commit('SET_FAN_COUNT', data.value); break
        case 'nciConfig[4]': context.$store.commit('SET_BTNONOFF_MODE', data.value); break
        case 'nciConfig[6]': context.$store.commit('SET_TEMP_UNITS', data.value); break
        case 'nciConfig[7]': context.$store.commit('SET_TEMPREG_MODE', data.value); break
        case 'nciConfig[8]': context.$store.commit('SET_PROP_BAND', data.value); break
        case 'nciConfig[9]': context.$store.commit('SET_OPEN_TO_SPEED2', data.value); break
        case 'nciConfig[10]': context.$store.commit('SET_OPEN_TO_SPEED3', data.value); break
        case 'nciConfig[12]': context.$store.commit('SET_OPEN_TO_SPEED1', data.value); break
        case 'nciEnables[0]': context.$store.commit('SET_HITEMPALARM_ENABLE', data.value); break
        case 'nciEnables[1]': context.$store.commit('SET_LOWTEMPALARM_ENABLE', data.value); break
        case 'nciEnables[4]': context.$store.commit('SET_BTN_ONOFF_ENABLE', data.value); break
        case 'nciEnables[5]': context.$store.commit('SET_BTN_UPDN_ENABLE', data.value); break
        case 'nciEnables[6]': context.$store.commit('SET_BTN_MODE_ENABLE', data.value); break
        case 'nciEnables[7]': context.$store.commit('SET_BTN_UNITS_ENABLE', data.value); break
        case 'nciEnables[8]': context.$store.commit('SET_BTN_FANSPD_ENABLE', data.value); break
        case 'nciEnables[9]': context.$store.commit('SET_SHOWTEMP_ENABLE', data.value); break
        case 'nciEnables[13]':context.$store.commit('SET_CARDSLOT_IN_ENABLE', data.value); break
        case 'nciEnables[14]':context.$store.commit('SET_WINDOW_IN_ENABLE', data.value); break
        case 'nciEnables[15]':context.$store.commit('SET_DOOR_IN_ENABLE', data.value); break
        case 'nciEnables[16]':context.$store.commit('SET_SERVICE_OUT_ENABLE', data.value); break
        case 'nciTiempos[5]': context.$store.commit('SET_WINDOWOPEN_TIME', data.value); break
        case 'nciTiempos[6]': context.$store.commit('SET_DOOROPEN_TIME', data.value); break
        case 'nciTiempos[7]': context.$store.commit('SET_DRWINCLOSED_TIME', data.value); break
        case 'nciTiempos[9]': context.$store.commit('SET_CARD_OUT_TIME', data.value); break
        case 'nciTiempos[10]': context.$store.commit('SET_MOV_STOP_TIME', data.value); break
        case 'nciTiempos[11]': context.$store.commit('SET_STANDBY_TIME', data.value); break
        case 'nciDebounce': context.$store.commit('SET_DEBOUNCE_TIME', data.value); break
        case 'nciTemperaturas[0]': context.$store.commit('SET_HITEMP_THRH', data.value); break
        case 'nciTemperaturas[1]': context.$store.commit('SET_LOWTEMP_THRH', data.value); break
        case 'nciTemperaturas[2]': context.$store.commit('SET_TEMP_RIPPLE', data.value); break
        case 'nciTemperaturas[3]': context.$store.commit('SET_SPEED2_TEMP', data.value); break
        case 'nciTemperaturas[4]': context.$store.commit('SET_SPEED3_TEMP', data.value); break
        case 'nciTemperaturas[5]': context.$store.commit('SET_SETPNT_STANDBY', data.value); break
        case 'nciTemperaturas[6]': context.$store.commit('SET_SETPNT_UNOCCUP', data.value); break
        case 'nciTemperaturas[7]': context.$store.commit('SET_SETPNT_RIPPLE', data.value); break
        case 'nciTemperaturas[8]': context.$store.commit('SET_MAX_SETPOINT', data.value); break
        case 'nciTemperaturas[9]': context.$store.commit('SET_MIN_SETPOINT', data.value); break
        case 'nciTiempos[0]': context.$store.commit('SET_PREVENT_TIME', data.value); break
        case 'nciTiempos[1]': context.$store.commit('SET_POSTVENT_TIME', data.value); break
        case 'nciTiempos[2]': context.$store.commit('SET_TURNOFF_TIME', data.value); break
        case 'nciTiempos[3]': context.$store.commit('SET_TURNON_TIME', data.value); break
        case 'nciTiempos[4]': context.$store.commit('SET_TEMPALARM_BEATS', data.value); break
        case 'nvoAlrDefTemp':
        case 'n1AlrDefTemp':
          context.$store.commit('SET_LOWTEMP_ALARM', data.value)
          break
        case 'nvoAlrExcTemp':
        case 'n1AlrExcTemp':
          context.$store.commit('SET_HITEMP_ALARM', data.value)
          break
      }
    },

    dbSelectOne (my, dbName, data) {
      var msg
      switch (dbName) {
        case 'devices':
          my.$store.commit('SAVE_DEVICE_DATA', { id: data[0].id, node: data[0].node, nid: data[0].nid })
          break
        case 'fancoil':
          my.$store.commit('SET_FANCOIL_DATA', {
            fanXpos: data[0].fanXpos,
            fanYpos: data[0].fanYpos,
            fanXsize: data[0].fanXsize,
            coolValveXpos: data[0].coolValveXpos,
            coolValveYpos: data[0].coolValveYpos,
            coolValveXsize: data[0].coolValveXsize,
            heatValveXpos: data[0].heatValveXpos,
            heatValveYpos: data[0].heatValveYpos,
            heatValveXsize: data[0].heatValveXsize
          })
          msg = { type: 'db.selectOne', dbName: 'pirsensors', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'pirsensors':
          my.$store.commit('SET_PIR_DATA', {
            pirXpos: data[0].pirXpos,
            pirYpos: data[0].pirYpos,
            pirXsize: data[0].pirXsize
          })
          msg = { type: 'db.selectOne', dbName: 'accesses', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'accesses':
          my.$store.commit('SET_ACCESSES_DATA', {
            doorXpos: data[0].doorXpos,
            doorYpos: data[0].doorYpos,
            doorXsize: data[0].doorXsize,
            winXpos: data[0].winXpos,
            winYpos: data[0].winYpos,
            winXsize: data[0].winXsize
          })
          msg = { type: 'db.selectOne', dbName: 'cardslots', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'cardslots':
          my.$store.commit('SET_CARDSLOT_DATA', {
            slotXpos: data[0].slotXpos,
            slotYpos: data[0].slotYpos,
            slotXsize: data[0].slotXsize
          })
          msg = { type: 'db.selectOne', dbName: 'generaldata', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'generaldata':
          my.$store.commit('SET_UNITSTATUS_DATA', { tbXpos: data[0].tbXpos, tbYpos: data[0].tbYpos })
          msg = { type: 'db.selectOne', dbName: 'temperatures', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'temperatures':
          my.$store.commit('SET_TEMP_DATA', { consigneXpos: data[0].consigneXpos, consigneYpos: data[0].consigneYpos })
          msg = { type: 'db.selectOne', dbName: 'setpoints', whr: `device=${this.$store.state.device.id}` }
          my.$store.commit('SEND_MESSAGE', msg)
          break
        case 'setpoints':
          my.$store.commit('SET_SETPOINT_DATA', { setpntXpos: data[0].setpntXpos, setpntYpos: data[0].setpntYpos })
          break
        case 'nvdata': my.$store.commit('SET_NETVARS_FROM_DB', data); break
      }
    },

    showMessageLine (context, data) {
      context.$store.commit('SET_MESSAGE_LINE', data)
      if (data.code === 1) alert(data.msg)
    }
  },

  watch: { '$store.state.serverMsg': 'sendMessage' }
}
</script>



