<template>
  <div class="ui left aligned raised container segment" :class="color">
    <div class="ui pointing menu">
      <div class="ui container">
        <div class="item">
          <select class="ui dropdown" v-model="node">
            <option v-for="(dev, idx) in devices" :value="dev.node" :key="idx">N{{dev.node}} - {{dev.name}}</option>
          </select>
        </div>
        <div class="item">
          <button :class="primaryBtnState" @click="refreshCurrentValues">
            <i class="ui upload icon"></i>
            Traer Confg. Actual
          </button>
        </div>
        <div class="right menu">
          <div class="item">
            <button :class="primaryBtnState" @click="saveAlarmConfig">
              <i class="ui download icon"></i>
                Enviar Confg. al Nodo
            </button>
          </div>
        </div>
      </div>
    </div>
    <form class="ui form">
      <table class="ui basic table">
        <tbody>
          <tr><td colspan="3"><img src="../../static/images/AutoRegulation.png" height="100%"></td></tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="T.Exceso" v-model="nciTemperaturas0" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>Umbral de Alarma de Temperatura por Exceso</td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="T.Defecto" v-model="nciTemperaturas1" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>Umbral de Alarma de Temperatura por Defecto</td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Hist&eacute;resis" v-model="nciTemperaturas2" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>Hist&eacute;resis de temperatura para las alarmas por exceso y defecto de temperatura</td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Veloc.2" v-model="nciTemperaturas3" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>Diferencial de temperatura para la activacin de la velocidad 2 de fancoil.</p>
              <p>Ej: si T.Ambiente=27, T.Consigna=26 y esta variable=2.00 --> Veloc.Fancoil = 1</p>
              <p>luego, si se baja T.Consigna=25 --> Veloc.Fancoil = 2</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Veloc.3" v-model="nciTemperaturas4" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>Diferencial de temperatura para la activacin de la velocidad 3 de fancoil.</p>
              <p>Ej: si T.Ambiente=27, T.Consigna=26 y esta variable=4.00 --> Veloc.Fancoil = 1</p>
              <p>luego, si se baja T.Consigna=23 --> Veloc.Fancoil = 3</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="T.Offset" v-model="nciTemperaturas5" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>Offset de temperatura de consigna para regulacin en estado STANDBY.</p>
              <p>Ej: si el Termostato est en estado de CONFORT, Modo Frio, T.Consigna=24 y esta Variable=4.00</p>
              <p>al pulsar el bot&oacute;n ON/OFF y pasar el Termostato a STANDBY, la nueva T.Consigna ser&aacute; 28, si vuelvo a encender, la T.Consigna ser&aacute; nuevamente 24</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="T.desocup." v-model="nciTemperaturas6" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>Offset de temperatura de consigna para regulacin en estado DESOCUPACION.</p>
              <p>...similar al anterior, pero cuando el termostato est&aacute; en estado Desocupaci&oacute;n</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Histeresis" v-model="nciTemperaturas7" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td><p>Hist&eacute;resis sobre la temperatura de consigna para la regulacin de temperatura.</p></td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="MinConsigna" v-model="nciTemperaturas9" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>M&iacute;nimo que puede alcanzar la temperatura de consigna.</p>
              <p>...es necesario reiniciar el equipo al cambiarla</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="MaxConsigna" v-model="nciTemperaturas8" min="0" max="10000" required>
              </div>
            </td>
            <td><sup>o</sup>C</td>
            <td>
              <p>M&aacute;ximo que puede alcanzar la temperatura de consigna.</p>
              <p>...es necesario reiniciar el equipo al cambiarla</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Pre-Ventilacion" v-model="nciTiempos0" min="0" max="65535" required>
              </div>
            </td>
            <td>Seg.</td>
            <td>
              <p>Tiempo de Pre-ventilaci&oacute;n</p>
              <p>...es el tiempo que se deja pasar desde que se activa la salida correspondiente a la regulacin de temperatura (Compresor o vlvula) hasta que se activa la ventilacin del fancoil.</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Post-Ventilacion" v-model="nciTiempos1" min="0" max="65535" required>
              </div>
            </td>
            <td>Seg.</td>
            <td>
              <p>Tiempo de Post-ventilaci&oacute;n</p>
              <p>...es el tiempo que se deja pasar desde que se desactiva la salida correspondiente a la regulacin de temperatura (Compresor o vlvula) hasta que se desactiva la ventilacin del fancoil</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Retardo-OFF" v-model="nciTiempos2" min="0" max="65535" required>
              </div>
            </td>
            <td>Seg.</td>
            <td>
              <p>Tiempo m&iacute;nimo en segundos que debe permanecer encendido el equipo (Compresor, Expansor, etc.) antes de apagarse. Para aumentar la vida &uacute;til de la m&aacute;quina</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Retardo-ON" v-model="nciTiempos3" min="0" max="65535" required>
              </div>
            </td>
            <td>Seg.</td>
            <td>
              <p>Tiempo m&iacute;nimo en segundos que debe esperarse antes del re-encendido del equipo. Para aumentar la vida &uacute;til de la m&aacute;quina</p>
            </td>
          </tr>
          <tr>
            <td>
              <div :class="inputState">
                <input type="number" placeholder="Latido" v-model="nciTiempos4" min="0" max="65535" required>
              </div>
            </td>
            <td>Seg.</td>
            <td>Tiempo de latido de las alarmas de Temperatura y Humedad</td>
          </tr>
        </tbody>
      </table>
    </form>
  </div>
</template>

<script>

export default {

  data () {
    return {
      node: 0,
      devices: [],
      nciTemperaturas0: '',
      nciTemperaturas1: '',
      nciTemperaturas2: '',
      nciTemperaturas3: '',
      nciTemperaturas4: '',
      nciTemperaturas5: '',
      nciTemperaturas6: '',
      nciTemperaturas7: '',
      nciTemperaturas8: '',
      nciTemperaturas9: '',
      nciTiempos0: '',
      nciTiempos1: '',
      nciTiempos2: '',
      nciTiempos3: '',
      nciTiempos4: ''
    }
  },

  created () {
    this.refreshCurrentValues()
    this.loadDevices()
  },

  methods: {
    loadDevices () {
      this.color = 'red'
      var msg = { type: 'db.getDevices', queryid: 'CLI-GETDEVICES' }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    devicesArrived () {
      if (!this.$store.state.db.devices.length) return
      this.color = 'green'
      var devices = []
      for (var i = 0; i < this.$store.state.db.devices.length; i++) {
        if (this.$store.state.db.devices[i].node) {
          devices.push(this.$store.state.db.devices[i])
        }
      }
      this.devices = devices
      this.$store.commit('SET_DEVICES_FROM_DB', null)
    },

    getCfgPackNvs () {
      var msg = {
        type: 'getNvPackValues',
        node: this.node,
        nvPack: 'nciTemperaturas[0],nciTemperaturas[1],nciTemperaturas[2],nciTemperaturas[3],nciTemperaturas[4],nciTemperaturas[5],nciTemperaturas[6],nciTemperaturas[7],nciTemperaturas[8],nciTemperaturas[9],nciTiempos[0],nciTiempos[1],nciTiempos[2],nciTiempos[3],nciTiempos[4]'
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    refreshCurrentValues () {
      this.nciTemperaturas0 = this.$store.state.alarms.nciTemperaturas0 / 100
      this.nciTemperaturas1 = this.$store.state.alarms.nciTemperaturas1 / 100
      this.nciTemperaturas2 = this.$store.state.alarms.nciTemperaturas2 / 100
      this.nciTemperaturas3 = this.$store.state.alarms.nciTemperaturas3 / 100
      this.nciTemperaturas4 = this.$store.state.alarms.nciTemperaturas4 / 100
      this.nciTemperaturas5 = this.$store.state.alarms.nciTemperaturas5 / 100
      this.nciTemperaturas6 = this.$store.state.alarms.nciTemperaturas6 / 100
      this.nciTemperaturas7 = this.$store.state.alarms.nciTemperaturas7 / 100
      this.nciTemperaturas8 = this.$store.state.alarms.nciTemperaturas8 / 100
      this.nciTemperaturas9 = this.$store.state.alarms.nciTemperaturas9 / 100
      this.nciTiempos0 = this.$store.state.alarms.nciTiempos0
      this.nciTiempos1 = this.$store.state.alarms.nciTiempos1
      this.nciTiempos2 = this.$store.state.alarms.nciTiempos2
      this.nciTiempos3 = this.$store.state.alarms.nciTiempos3
      this.nciTiempos4 = this.$store.state.alarms.nciTiempos4
    },

    saveAlarmConfig () {
      this.sendConfigNv({ nvName: 'nciTemperaturas[0]', nvValue: this.nciTemperaturas0 })
    },

    sendConfigNv (nvData) {
      var msg = {
        type: 'setNetvarValue',
        node: this.node,
        nvName: nvData.nvName,
        value: nvData.nvValue
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    processReplay () {
      switch (this.$store.state.replayData.NvName) {
        case 'nciTemperaturas[0]': this.sendConfigNv({nvName: 'nciTemperaturas[1]', nvValue: this.nciTemperaturas1 * 100}); break
        case 'nciTemperaturas[1]': this.sendConfigNv({nvName: 'nciTemperaturas[2]', nvValue: this.nciTemperaturas2 * 100}); break
        case 'nciTemperaturas[2]': this.sendConfigNv({nvName: 'nciTemperaturas[3]', nvValue: this.nciTemperaturas3 * 100}); break
        case 'nciTemperaturas[3]': this.sendConfigNv({nvName: 'nciTemperaturas[4]', nvValue: this.nciTemperaturas4 * 100}); break
        case 'nciTemperaturas[4]': this.sendConfigNv({nvName: 'nciTemperaturas[5]', nvValue: this.nciTemperaturas5 * 100}); break
        case 'nciTemperaturas[5]': this.sendConfigNv({nvName: 'nciTemperaturas[6]', nvValue: this.nciTemperaturas6 * 100}); break
        case 'nciTemperaturas[6]': this.sendConfigNv({nvName: 'nciTemperaturas[7]', nvValue: this.nciTemperaturas7 * 100}); break
        case 'nciTemperaturas[7]': this.sendConfigNv({nvName: 'nciTemperaturas[8]', nvValue: this.nciTemperaturas8 * 100}); break
        case 'nciTemperaturas[8]': this.sendConfigNv({nvName: 'nciTemperaturas[9]', nvValue: this.nciTemperaturas9 * 100}); break
        case 'nciTemperaturas[9]': this.sendConfigNv({nvName: 'nciTiempos[0]', nvValue: this.nciTiempos0}); break
        case 'nciTiempos[0]': this.sendConfigNv({nvName: 'nciTiempos[1]', nvValue: this.nciTiempos1}); break
        case 'nciTiempos[1]': this.sendConfigNv({nvName: 'nciTiempos[2]', nvValue: this.nciTiempos2}); break
        case 'nciTiempos[2]': this.sendConfigNv({nvName: 'nciTiempos[3]', nvValue: this.nciTiempos3}); break
        case 'nciTiempos[3]': this.sendConfigNv({nvName: 'nciTiempos[4]', nvValue: this.nciTiempos4}); break
        case 'nciTiempos[4]': this.getCfgPackNvs()
      }
    }
  },

  computed: {
    primaryBtnState () { return this.node ? 'ui primary basic button' : 'ui disabled button' },
    inputState () { return this.node ? 'ui input' : 'ui disabled input' }
  },

  watch: {
    'node': 'getCfgPackNvs',
    '$store.state.alarms.nciTemperaturas0': function () { this.nciTemperaturas0 = this.$store.state.alarms.nciTemperaturas0 / 100 },
    '$store.state.alarms.nciTemperaturas1': function () { this.nciTemperaturas1 = this.$store.state.alarms.nciTemperaturas1 / 100 },
    '$store.state.alarms.nciTemperaturas2': function () { this.nciTemperaturas2 = this.$store.state.alarms.nciTemperaturas2 / 100 },
    '$store.state.alarms.nciTemperaturas3': function () { this.nciTemperaturas3 = this.$store.state.alarms.nciTemperaturas3 / 100 },
    '$store.state.alarms.nciTemperaturas4': function () { this.nciTemperaturas4 = this.$store.state.alarms.nciTemperaturas4 / 100 },
    '$store.state.alarms.nciTemperaturas5': function () { this.nciTemperaturas5 = this.$store.state.alarms.nciTemperaturas5 / 100 },
    '$store.state.alarms.nciTemperaturas6': function () { this.nciTemperaturas6 = this.$store.state.alarms.nciTemperaturas6 / 100 },
    '$store.state.alarms.nciTemperaturas7': function () { this.nciTemperaturas7 = this.$store.state.alarms.nciTemperaturas7 / 100 },
    '$store.state.alarms.nciTemperaturas8': function () { this.nciTemperaturas8 = this.$store.state.alarms.nciTemperaturas8 / 100 },
    '$store.state.alarms.nciTemperaturas9': function () { this.nciTemperaturas9 = this.$store.state.alarms.nciTemperaturas9 / 100 },
    '$store.state.alarms.nciTiempos0': function () { this.nciTiempos0 = this.$store.state.alarms.nciTiempos0 },
    '$store.state.alarms.nciTiempos1': function () { this.nciTiempos1 = this.$store.state.alarms.nciTiempos1 },
    '$store.state.alarms.nciTiempos2': function () { this.nciTiempos2 = this.$store.state.alarms.nciTiempos2 },
    '$store.state.alarms.nciTiempos3': function () { this.nciTiempos3 = this.$store.state.alarms.nciTiempos3 },
    '$store.state.alarms.nciTiempos4': function () { this.nciTiempos4 = this.$store.state.alarms.nciTiempos4 },
    '$store.state.replies': 'processReplay',
    '$store.state.db.devices': 'devicesArrived'
  }
}
</script>



