<template>
  <div class="ui left aligned raised container segment" :class="color">
    <div v-if="action == 'browse'">
      <div class="ui pointing menu">
        <div class="item">
          <select class="ui dropdown" v-model="device">
            <option v-for="(dev, idx) in devices" :value="dev.node" v-bind:key="idx">N{{dev.node}} - {{dev.name}}</option>
          </select>
        </div>
        <div class="item">
          <button class="ui primary basic button" @click="btnNewNetVar">
            <i class="large plus icon"></i>Nueva Variable de Red
          </button>
        </div>
        <div v-if="toDelete" class="right menu">
          <div class="item">
            <button class="ui negative basic button" @click="deleteNetVar"><i class="remove icon"></i>Borrar</button>
          </div>
        </div>
      </div>
      <table class="ui tablet unstackable selectable table">
        <thead>
          <tr>
            <th>Nodo</th><th>Variable de Red</th><th>Tipo</th><th>Indice</th><th>Selector</th>
            <th class="center aligned">Editar</th>
            <th class="center aligned">Borrar</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(nv, idx) in netVars" v-bind:key="idx">
            <td>{{nv.device}}</td>
            <td>{{nv.nvName}}</td>
            <td>{{nv.type}}</td>
            <td>{{nv.nvIndex}}</td>
            <td>{{nv.selector}}</td>
            <td class="center aligned"><a @click="btnModify(nv)"><i class="large link edit icon"></i></a></td>
            <td class="center aligned">
              <input v-model="nv.selected" class="ui checkbox" type="checkbox" @change="toDeleteClicked">
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <editNv v-else-if="action == 'edit'" :edtNv="editNv" :devices="devices" :title="actionTitle" @go_back="go_back" />
  </div>
</template>

<script>
import editNv from '../components/Setup-LonNodes-EditNv.vue'

export default {

  data () {
    return {
      color: 'yellow',
      action: 'browse',
      actionTitle: '',
      devices: [],
      device: 128,
      netVars: [],
      editNv: null,
      toDelete: false
    }
  },

  mounted () {
    this.loadNodes()
  },

  methods: {
    loadNodes () {
      this.color = 'red'
      var msg = { type: 'db.getDevices', queryid: 'CLI-GETDEVICES' }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    devicesArrived () {
      if (this.$store.state.db.devices) {
        this.devices = []
        this.devices = [ {id: 0, node: 128, nid: '', name: '** TODOS **'} ]
        for (var i = 0; i < this.$store.state.db.devices.length; i++) {
          this.devices.push(this.$store.state.db.devices[i])
        }
        this.loadNetVars()
        this.$store.commit('SET_DEVICES_FROM_DB', null)
      }
    },

    loadNetVars () {
      this.toDelete = false
      var msg = this.device === 128 ? { type: 'db.getNetVars', queryid: 'CLI-GETNETVARS' } : {
        type: 'db.selectOne',
        dbName: 'nvdata',
        whr: `device=${this.device}`
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    nvdataArrived () {
      if (this.$store.state.db.netVars) {
        this.color = 'green'
        var nVars = []
        for (var i = 0; i < this.$store.state.db.netVars.length; i++) {
          nVars.push({
            id: this.$store.state.db.netVars[i].id,
            device: this.$store.state.db.netVars[i].device,
            nvName: this.$store.state.db.netVars[i].nvName,
            nvIndex: this.$store.state.db.netVars[i].nvIndex,
            selector: this.$store.state.db.netVars[i].selector,
            type: this.$store.state.db.netVars[i].type,
            selected: false
          })
        }
        this.netVars = nVars
      }
      this.$store.commit('SET_NETVARS_FROM_DB', null)
    },

    loadNetVarsold () {
      this.color = 'yellow'
      this.toDelete = false
      var url = this.device === 128 ? 'api/nvData' : `api/nvData/${this.device}`
      this.$http.get(url).then(function (resp) {
        this.color = 'green'
        var nVars = []
        for (var i = 0; i < resp.data.length; i++) {
          nVars.push({
            id: resp.data[i].id,
            device: resp.data[i].device,
            nvName: resp.data[i].nvName,
            nvIndex: resp.data[i].nvIndex,
            selector: resp.data[i].selector,
            type: resp.data[i].type,
            selected: false
          })
        }
        this.netVars = nVars
      }).catch(function (err) {
        this.color = 'red'
        console.log(err)
      })
    },

    btnNewNetVar () {
      this.actionTitle = 'Nueva variable de red'
      this.editNv = { id: 0, device: '', nvName: '', nvIndex: '', selector: '', type: '' }
      this.action = 'edit'
    },

    btnModify (nv) {
      this.actionTitle = 'Modificar variable de red'
      this.editNv = nv
      this.action = 'edit'
    },

    go_back (retData) {
      if (retData) {
        var nVar = this.netVars.find((nv) => nv.id === retData.id)
        if (nVar) {
          nVar.device = retData.device
          nVar.nvName = retData.nvName
          nVar.nvIndex = retData.nvIndex
          nVar.selector = retData.selector
          nVar.type = retData.type
        } else this.netVars.push(retData)
      }
      this.action = 'browse'
    },

    toDeleteClicked () {
      var nVar = this.netVars.find(nv => nv.selected)
      this.toDelete = nVar !== null && nVar !== undefined
    },

    deleteNetVar () { }
  },

  watch: {
    'device': 'loadNetVars',
    '$store.state.db.devices': 'devicesArrived',
    '$store.state.db.netVars': 'nvdataArrived'
  },

  components: { editNv }
}
</script>



