<template>
  <div v-bind:style="tbInfoPosition">
    <div class="ui image" :draggable="$store.state.mapSetup.movable" v-on:dragstart="drag">
      <table class="ui celled table">
        <tbody>
          <tr>
            <td rowspan=2><img :src="unitModeImage" width="40"></td>
            <td><div class="basic label"><i :class="ecoModeImg"></i>{{ecoMode}}</div></td>
          </tr>
          <tr><td><div class="basic label">{{unitMode}}</div></td></tr>
          <tr>
            <td><img :src="unitStateImage" width="40"></td>
            <td><div class="basic label">{{unitState}}</div></td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  methods: {
    drag: function (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: 'generalInfo' })
    }
  },

  computed: {
    ...mapGetters([ 'tbInfoPosition', 'unitStateImage', 'unitModeImage', 'ecoModeImg' ]),

    ecoMode () {
      if (this.$store.state.generalInfo.ecoMode === -1) return
      if (this.$store.state.generalInfo.ecoMode === 0) return 'REGULANDO TEMPERATURA'
      return 'ECONOMICO'
    },

    unitMode () {
      switch (this.$store.state.generalInfo.unitMode) {
        case 1: return 'MODO CALOR'
        case 3: return 'MODO FRIO'
        case 9: return 'MODO SOLO VENTILADOR'
        case 14: return 'MODO AUTOMATICO'
      }
    },

    unitState () {
      switch (this.$store.state.generalInfo.unitState) {
        case 0: return 'ESTADO DE CONFORT'
        case 1: return 'ESTADO STANDBY'
        case 3: return 'ESTADO DESOCUPADO'
      }
    }
  }
}
</script>



