<template>
  <div class="ui left aligned raised container segment" :class="color">
    <div class="ui pointing menu">
      <div class="ui container">
        <div class="item">
          <select class="ui dropdown" v-model="node">
            <option v-for="(dev, idx) in devices" :value="dev.node" :key="idx">N{{dev.node}} - {{dev.name}}</option>
          </select>
        </div>
        <div class="item">
          <button :class="primaryBtnState" @click="loadCurrendValues"><i class="ui upload icon"></i>Traer Confg. Actual</button>
        </div>
        <div class="right menu">
          <div class="item">
            <button :class="primaryBtnState" @click="SendConfigToNode"><i class="ui download icon"></i>Enviar Confg. al Nodo</button>
          </div>
        </div>
      </div>
    </div>
    <form class="ui form">
      <div class="ui message">
        <div :class="hasFans ? 'ui two column doubling stackable grid container' : 'ui doubling stackable grid container'">
          <div class="column">
            <div class="ui fluid teal horizontal label"> Qu&eacute; va a controlar ?</div>
            <div :class="fieldState">
              <select class="ui dropdown" v-model="opMode" @change="opModeChanged">
                <option v-for="opm in operModes" :value="opm.id" :key="opm.id">{{opm.desc}}</option>
              </select>
            </div>
          </div>
          <div v-if="hasFans" class="column">
            <div class="ui fluid teal horizontal label">Ventiladores</div>
            <div class="inline fields" style="margin-top: 5px">
              <div class="ui three column doubling stackable grid container">
                <div class="column">
                  <div :class="fieldState">
                    <div class="ui radio checkbox">
                      <input class="input" type="radio" value=0 v-model="nciConfig3">
                      <label>3 Velocidades</label>
                    </div>
                  </div>
                </div>
                <div class="column">
                  <div :class="fieldState">
                    <div class="ui radio checkbox">
                      <input class="input" type="radio" value=2 v-model="nciConfig3">
                      <label>2 Velocidades</label>
                    </div>
                  </div>
                </div>
                <div class="column">
                  <div :class="fieldState">
                    <div class="ui radio checkbox">
                      <input class="input" type="radio" value=1 v-model="nciConfig3">
                      <label>1 Velocidad</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="ui message">
        <div class="ui three column doubling stackable grid container">
          <div class="column">
            <div class="ui fluid gray label">Tipo de M&aacute;quina</div>
            <div class="ui list">
              <div v-for="(mach, idx) in machTypes" class="item" :key="idx">
                <i :class="mach.selected ? 'ui check square icon' : 'ui square outline icon'"></i>{{mach.name}}
              </div>
            </div>
          </div>
          <div class="column">
            <div class="ui fluid gray label">Tipo de V&aacute;lvula de Frio Conectada</div>
            <div class="ui list">
              <div v-for="(coolVv, idx) in coolValves" class="item" :key="idx">
                <i :class="coolVv.selected ? 'ui check square icon' : 'ui square outline icon'"></i>{{coolVv.name}}
              </div>
            </div>
          </div>
          <div class="column">
            <div class="ui fluid gray label">Tipo de V&aacute;lvula de Calor Conectada</div>
            <div class="ui list">
              <div v-for="(heatVv, idx) in heatValves" class="item" :key="idx">
                <i :class="heatVv.selected ? 'ui check square icon' : 'ui square outline icon'"></i>{{heatVv.name}}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="ui message">
        <div class="ui fluid teal horizontal label">Tipo de Regulaci&oacute;n que realiza el algoritmo de clima</div>
        <div class="ui grouped fields" style="margin-top: 5px">
          <div :class="fieldState">
            <div class="ui radio checkbox">
              <input class="input" type="radio" value=0 v-model="nciConfig7">
              <label><span class="caps">Todo/Nada</span> - Funciona como un termostato convencional.</label>
            </div>
          </div>
          <div :class="fieldState">
            <div class="ui radio checkbox">
              <input class="input" type="radio" value=1 v-model="nciConfig7">
              <label><span class="caps">PI</span> - Algoritmo Proporcional-integral (Solo con vlvulas proporcionales PWM en fancoil de 2 tubos de frio, 2 tubos de calor o 4 tubos).</label>
            </div>
          </div>
        </div>
        <div v-if="nciConfig7 == 1">
          <table>
            <tbody>
              <tr>
                <td>
                  <div class="inline fields">
                    <div :class="inputState">
                      <input type="number" v-model="nciConfig8" min="0" max="65535" required>
                    </div>
                  </div>
                </td>
                <td>Banda proporcional para el lazo de control</td>
              </tr>
              <tr>
                <td>
                  <div class="inline fields">
                    <div :class="inputState"><input type="number" v-model="nciConfig12" min="0" max="65535" required></div>
                    <label style="margin-left: 10px;">%</label>
                  </div>
                </td>
                <td>Porcentaje de apertura de la vlvula por encima del cual se activa la Velocidad 1 del fancoil</td>
              </tr>
              <tr>
                <td>
                  <div class="inline fields">
                    <div :class="inputState"><input type="number" v-model="nciConfig9" min="0" max="65535" required></div>
                    <label style="margin-left: 10px;">%</label>
                  </div>
                </td>
                <td>Porcentaje de apertura de la vlvula por encima del cual se activa la Velocidad 2 del fancoil</td>
              </tr>
              <tr>
                <td>
                  <div class="inline fields">
                    <div :class="inputState"><input type="number" v-model="nciConfig10" min="0" max="65535" required></div>
                    <label style="margin-left: 10px;">%</label>
                  </div>
                </td>
                <td>Porcentaje de apertura de la vlvula por encima del cual se activa la Velocidad 3 del fancoil</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {

  data () {
    return {
      color: 'yellow',
      operModes: [
        { id: 4, desc: 'Fancoil 2 tubos solo frio. Valvula N.A.', nciCfg0: 0, nciCfg1: 0, nciCfg2: 4 },
        { id: 14, desc: 'Fancoil 2 tubos solo frio. Valvula N.C.', nciCfg0: 0, nciCfg1: 1, nciCfg2: 4 },
        { id: 22, desc: 'Fancoil 2 tubos solo frio. Valvula 3ptos', nciCfg0: 0, nciCfg1: 2, nciCfg2: 2 },
        { id: 34, desc: 'Fancoil 2 tubos solo frio. Valvula PWM', nciCfg0: 0, nciCfg1: 3, nciCfg2: 4 },
        { id: 140, desc: 'Fancoil 2 tubos solo calor. Valvula N.A.', nciCfg0: 1, nciCfg1: 4, nciCfg2: 0 },
        { id: 141, desc: 'Fancoil 2 tubos solo calor. Valvula N.C.', nciCfg0: 1, nciCfg1: 4, nciCfg2: 1 },
        { id: 143, desc: 'Fancoil 2 tubos solo calor. Valvula PWM', nciCfg0: 1, nciCfg1: 4, nciCfg2: 3 },
        { id: 122, desc: 'Fancoil 2 tubos solo calor. Valvula 3ptos', nciCfg0: 1, nciCfg1: 2, nciCfg2: 2 },
        { id: 200, desc: 'Fancoil 4 tubos. V.Frio=N.A. V.calor=N.A.', nciCfg0: 2, nciCfg1: 0, nciCfg2: 0 },
        { id: 201, desc: 'Fancoil 4 tubos. V.Frio=N.A. V.calor=N.C.', nciCfg0: 2, nciCfg1: 0, nciCfg2: 1 },
        { id: 210, desc: 'Fancoil 4 tubos. V.Frio=N.C. V.calor=N.A.', nciCfg0: 2, nciCfg1: 1, nciCfg2: 0 },
        { id: 211, desc: 'Fancoil 4 tubos. V.Frio=N.C. V.calor=N.C.', nciCfg0: 2, nciCfg1: 1, nciCfg2: 1 },
        { id: 243, desc: 'Fancoil 4 tubos. Valvula de calor PWM', nciCfg0: 2, nciCfg1: 4, nciCfg2: 3 },
        { id: 233, desc: 'Fancoil 4 tubos. Valvulas Proporcionales', nciCfg0: 2, nciCfg1: 3, nciCfg2: 3 },
        { id: 300, desc: 'Fancoil 2 tubos intercambiable V.frio=N.A.', nciCfg0: 3, nciCfg1: 0, nciCfg2: 0 },
        { id: 311, desc: 'Fancoil 2 tubos intercambiable V.frio=N.C.', nciCfg0: 3, nciCfg1: 1, nciCfg2: 1 },
        { id: 322, desc: 'Fancoil 2 tubos intercambiable 3 puntos', nciCfg0: 3, nciCfg1: 2, nciCfg2: 2 },
        { id: 400, desc: 'Maquina Partida (solo frio)', nciCfg0: 4, nciCfg1: 0, nciCfg2: 0 },
        { id: 500, desc: 'Maquina Partida + Bomba de Calor', nciCfg0: 5, nciCfg1: 0, nciCfg2: 0 },
        { id: 600, desc: 'Bomba de Calor (solo calor)', nciCfg0: 6, nciCfg1: 0, nciCfg2: 0 }
      ],
      machTypes: [
        {id: 0, selected: false, name: 'Fancoil de 2 tubos de frio'},
        {id: 1, selected: false, name: 'Fancoil de 2 tubos de calor'},
        {id: 2, selected: false, name: 'Fancoil de 4 tubos'},
        {id: 3, selected: false, name: 'Fancoil de 2 tubos intercambiables'},
        {id: 4, selected: false, name: 'Maquina partida (solo frio)'},
        {id: 5, selected: false, name: 'Maquina partida + Bomba de calor'},
        {id: 6, selected: false, name: 'Bomba de calor'}
      ],
      coolValves: [
        {id: 0, selected: false, name: '2 puntos (normalmente abierta)'},
        {id: 1, selected: false, name: '2 puntos (normalmente cerrada)'},
        {id: 2, selected: false, name: '3 puntos'},
        {id: 3, selected: false, name: 'Proporcional PWM (solo modo PI)'},
        {id: 4, selected: false, name: 'Sin vlvula'}
      ],
      heatValves: [
        {id: 0, selected: false, name: '2 puntos (normalmente abierta)'},
        {id: 1, selected: false, name: '2 puntos (normalmente cerrada)'},
        {id: 2, selected: false, name: '3 puntos'},
        {id: 3, selected: false, name: 'Proporcional PWM (solo modo PI)'},
        {id: 4, selected: false, name: 'Sin vlvula'}
      ],
      opMode: 0,
      nciConfig0: 0,
      nciConfig1: 0,
      nciConfig2: 0,
      nciConfig3: '',
      nciConfig7: '',
      nciConfig8: '',
      nciConfig9: '',
      nciConfig10: '',
      nciConfig12: '',
      devices: [],
      node: 0,
      hasFans: false
    }
  },

  created () {
    this.loadCurrendValues()
    this.loadDevices()
  },

  methods: {
    loadDevices () {
      this.color = 'red'
      var msg = { type: 'db.getDevices', queryid: 'CLI-GETDEVICES' }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    devicesArrived () {
      if (!this.$store.state.db.devices.length) return
      this.color = 'green'
      var devices = []
      for (var i = 0; i < this.$store.state.db.devices.length; i++) {
        if (this.$store.state.db.devices[i].node) {
          devices.push(this.$store.state.db.devices[i])
        }
      }
      this.devices = devices
      this.$store.commit('SET_DEVICES_FROM_DB', null)
    },

    getCfgPackNvs () {
      var msg = {
        type: 'getNvPackValues',
        node: this.node,
        nvPack: 'nciConfig[0],nciConfig[1],nciConfig[2],nciConfig[3],nciConfig[7],nciConfig[8],nciConfig[9],nciConfig[10],nciConfig[12]'
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    loadCurrendValues () {
      this.nciConfig0 = this.$store.state.opMode.nciConfig0
      this.nciConfig1 = this.$store.state.opMode.nciConfig1
      this.nciConfig2 = this.$store.state.opMode.nciConfig2
      this.nciConfig3 = this.$store.state.opMode.nciConfig3
      this.nciConfig7 = this.$store.state.opMode.nciConfig7
      this.nciConfig8 = this.$store.state.opMode.nciConfig8
      this.nciConfig9 = this.$store.state.opMode.nciConfig9
      this.nciConfig10 = this.$store.state.opMode.nciConfig10
      this.nciConfig12 = this.$store.state.opMode.nciConfig12
      this.changedValue()
    },

    updateNciCfg0 () {
      this.nciConfig0 = this.$store.state.opMode.nciConfig0
      this.changedValue()
    },

    updateNciCfg1 () {
      this.nciConfig1 = this.$store.state.opMode.nciConfig1
      this.changedValue()
    },

    updateNciCfg2 () {
      this.nciConfig2 = this.$store.state.opMode.nciConfig2
      this.changedValue()
    },

    changedValue () {
      this.opMode = parseInt(this.nciConfig0) * 100 + parseInt(this.nciConfig1) * 10 + parseInt(this.nciConfig2)
      this.opModeChanged()
    },

    opModeChanged () {
      var dev = this.operModes.find(dv => dv.id === this.opMode)
      if (dev) {
        this.hasFans = dev.nciCfg0 < 4
        for (var i = this.machTypes.length - 1; i >= 0; i--) {
          this.machTypes[i].selected = this.machTypes[i].id === dev.nciCfg0
        }
        for (i = this.coolValves.length - 1; i >= 0; i--) {
          this.coolValves[i].selected = this.coolValves[i].id === dev.nciCfg1
        }
        for (i = this.coolValves.length - 1; i >= 0; i--) {
          this.heatValves[i].selected = this.heatValves[i].id === dev.nciCfg2
        }
      }
    },

    SendConfigToNode () {
      if (this.opMode) {
        var nVar = this.operModes.find((nv) => nv.id === this.opMode)
        if (nVar) {
          this.nciConfig0 = nVar.nciCfg0
          this.nciConfig1 = nVar.nciCfg1
          this.nciConfig2 = nVar.nciCfg2
        }
      }
      this.sendConfigNv({ nvName: 'nciConfig[0]', nvValue: this.nciConfig0 })
    },

    sendConfigNv (nvData) {
      var msg = {
        type: 'setNetvarValue',
        node: this.node,
        nvName: nvData.nvName,
        value: nvData.nvValue
      }
      this.$store.commit('SEND_MESSAGE', msg)
    },

    processReplay () {
      switch (this.$store.state.replayData.NvName) {
        case 'nciConfig[0]': this.sendConfigNv({ nvName: 'nciConfig[1]', nvValue: this.nciConfig1 }); break
        case 'nciConfig[1]': this.sendConfigNv({ nvName: 'nciConfig[2]', nvValue: this.nciConfig2 }); break
        case 'nciConfig[2]': this.sendConfigNv({ nvName: 'nciConfig[3]', nvValue: this.nciConfig3 }); break
        case 'nciConfig[3]': this.sendConfigNv({ nvName: 'nciConfig[7]', nvValue: this.nciConfig7 }); break
        case 'nciConfig[7]': this.sendConfigNv({ nvName: 'nciConfig[8]', nvValue: this.nciConfig8 }); break
        case 'nciConfig[8]': this.sendConfigNv({ nvName: 'nciConfig[9]', nvValue: this.nciConfig9 }); break
        case 'nciConfig[9]': this.sendConfigNv({ nvName: 'nciConfig[10]', nvValue: this.nciConfig10 }); break
        case 'nciConfig[10]': this.sendConfigNv({ nvName: 'nciConfig[12]', nvValue: this.nciConfig12 }); break
        case 'nciConfig[12]':
          this.$store.state.opMode.nciConfig0 = 0
          this.$store.state.opMode.nciConfig1 = 0
          this.$store.state.opMode.nciConfig2 = 0
          this.getCfgPackNvs()
          break
      }
    }
  },

  computed: {
    fieldState () { return this.node ? 'field' : 'disabled field' },
    inputState () { return this.node ? 'ui input' : 'ui disabled input' },
    primaryBtnState () { return this.node ? 'ui primary basic button' : 'ui disabled button' }
  },

  watch: {
    'node': 'getCfgPackNvs',
    '$store.state.opMode.nciConfig0': 'updateNciCfg0',
    '$store.state.opMode.nciConfig1': 'updateNciCfg1',
    '$store.state.opMode.nciConfig2': 'updateNciCfg2',
    '$store.state.opMode.nciConfig3': function () { this.nciConfig3 = this.$store.state.opMode.nciConfig3 },
    '$store.state.opMode.nciConfig7': function () { this.nciConfig7 = this.$store.state.opMode.nciConfig7 },
    '$store.state.opMode.nciConfig8': function () { this.nciConfig8 = this.$store.state.opMode.nciConfig8 },
    '$store.state.opMode.nciConfig9': function () { this.nciConfig9 = this.$store.state.opMode.nciConfig9 },
    '$store.state.opMode.nciConfig10': function () { this.nciConfig10 = this.$store.state.opMode.nciConfig10 },
    '$store.state.opMode.nciConfig12': function () { this.nciConfig12 = this.$store.state.opMode.nciConfig12 },
    '$store.state.replies': 'processReplay',
    '$store.state.db.devices': 'devicesArrived'
  }
}
</script>

<style>

.caps {
  font-variant: small-caps;
  font-weight: bold;
}

</style>



