<template>
  <div v-bind:style="doorPosition">
    <div class="ui image">
      <img :src="doorImage" :draggable="$store.state.mapSetup.movable" :style="imgSize" v-on:dragstart="drag">
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {

  data () {
    return {
      imgSize: { width: '100px' }
    }
  },

  created () {
    this.updateDoorSize()
  },

  methods: {
    updateDoorSize () {
      this.imgSize.width = this.$store.state.accesses.doorXsize + 'px'
    },

    drag (_evt) {
      _evt.dataTransfer.setData('text', _evt.target.id)
      var picTaken = {
        takeLeft: _evt.offsetX,
        takeTop: _evt.offsetY
      }
      this.$emit('reallocate', { pic: picTaken, name: 'door' })
    }
  },

  computed: mapGetters([ 'doorImage', 'doorPosition' ]),

  watch: {
    '$store.state.accesses.doorXsize': 'updateDoorSize'
  }
}
</script>



